import React, { useEffect, useRef, useState } from 'react'
import mapboxgl from 'mapbox-gl'
import { useTranslation } from 'react-i18next'
import { motion, AnimatePresence } from 'framer-motion'
import { useAppStore } from '../store/appStore'

// Note: In production, you would set this via environment variables
mapboxgl.accessToken = 'pk.eyJ1IjoiZXhhbXBsZSIsImEiOiJjbGV4YW1wbGUifQ.example' // Replace with your actual token

const Map3D = () => {
  const { t } = useTranslation()
  const mapContainer = useRef(null)
  const map = useRef(null)
  const { 
    mapCenter, 
    mapZoom, 
    isMapLoading, 
    mapError, 
    setMapLoading, 
    setMapError,
    selectedAttraction,
    soundEnabled
  } = useAppStore()
  
  const [isInitialized, setIsInitialized] = useState(false)

  useEffect(() => {
    if (map.current) return // Initialize map only once

    try {
      setMapLoading(true)
      
      // Initialize Mapbox map with globe projection
      map.current = new mapboxgl.Map({
        container: mapContainer.current,
        style: 'mapbox://styles/mapbox/dark-v11',
        center: mapCenter,
        zoom: mapZoom,
        projection: 'globe',
        antialias: true,
        preserveDrawingBuffer: true
      })

      // Add navigation controls
      map.current.addControl(new mapboxgl.NavigationControl(), 'top-right')
      
      // Add fullscreen control
      map.current.addControl(new mapboxgl.FullscreenControl(), 'top-right')

      // Map event handlers
      map.current.on('load', () => {
        setIsInitialized(true)
        setMapLoading(false)
        
        // Add atmosphere and stars
        addAtmosphere(map.current)
        addStars(map.current)
        addCountryBoundaries(map.current)
        
        // Add event listeners for interactions
        addMapInteractions()
      })

      map.current.on('error', (e) => {
        console.error('Mapbox error:', e)
        setMapError('Failed to load map')
        setMapLoading(false)
      })

      // Auto-rotation
      map.current.on('idle', () => {
        if (soundEnabled && !selectedAttraction) {
          startAutoRotation()
        }
      })

    } catch (error) {
      console.error('Error initializing map:', error)
      setMapError(error.message)
      setMapLoading(false)
    }

    return () => {
      if (map.current) {
        map.current.remove()
      }
    }
  }, [])

  // Update map center and zoom when state changes
  useEffect(() => {
    if (!map.current || !isInitialized) return
    
    map.current.easeTo({
      center: mapCenter,
      zoom: mapZoom,
      duration: 2000,
      easing: (t) => t
    })
  }, [mapCenter, mapZoom, isInitialized])

  const addAtmosphere = (map) => {
    // Add atmosphere layer
    map.addLayer({
      id: 'sky',
      type: 'sky',
      paint: {
        'sky-type': 'atmosphere',
        'sky-atmosphere-sun': [0.0, 90.0],
        'sky-atmosphere-sun-intensity': 15
      }
    })
  }

  const addStars = (map) => {
    // Add stars background (simplified)
    map.addLayer({
      id: 'stars',
      type: 'background',
      paint: {
        'background-color': 'rgba(0, 0, 0, 0.8)',
        'background-opacity': 0.1
      }
    })
  }

  const addCountryBoundaries = (map) => {
    // Add country boundaries
    map.addSource('countries', {
      type: 'vector',
      url: 'mapbox://mapbox.mapbox-streets-v8'
    })

    map.addLayer({
      id: 'country-borders',
      type: 'line',
      source: 'countries',
      'source-layer': 'admin_0_boundary_lines_land',
      paint: {
        'line-color': '#00D4FF',
        'line-width': 0.5,
        'line-opacity': 0.6
      }
    })

    // Add country labels
    map.addLayer({
      id: 'country-labels',
      type: 'symbol',
      source: 'countries',
      'source-layer': 'admin_0_countries',
      layout: {
        'text-field': ['get', 'name_en'],
        'text-font': ['DIN Pro Medium - Regular'],
        'text-size': 10
      },
      paint: {
        'text-color': '#E4E4E7',
        'text-opacity': 0.8
      }
    })
  }

  const addMapInteractions = () => {
    if (!map.current) return

    // Click event to show location info
    map.current.on('click', (e) => {
      const { lng, lat } = e.lngLat
      
      // Create popup
      const popup = new mapboxgl.Popup({ closeOnClick: false })
        .setLngLat([lng, lat])
        .setHTML(`
          <div class="p-3">
            <h3 class="font-montserrat font-semibold text-text-primary mb-2">
              Coordinates
            </h3>
            <p class="text-sm text-text-secondary">
              Lat: ${lat.toFixed(4)}<br>
              Lng: ${lng.toFixed(4)}
            </p>
          </div>
        `)
        .addTo(map.current)

      // Remove popup after 3 seconds
      setTimeout(() => {
        popup.remove()
      }, 3000)
    })

    // Mouse enter/leave for country highlighting
    map.current.on('mouseenter', 'country-borders', () => {
      map.current.getCanvas().style.cursor = 'pointer'
    })

    map.current.on('mouseleave', 'country-borders', () => {
      map.current.getCanvas().style.cursor = ''
    })
  }

  const startAutoRotation = () => {
    if (!map.current || selectedAttraction) return

    let rotation = 0
    const rotate = () => {
      if (!map.current || selectedAttraction) return
      
      rotation += 0.1
      map.current.rotateTo(rotation, { duration: 0 })
      
      if (rotation >= 360) {
        rotation = 0
      }
      
      requestAnimationFrame(rotate)
    }
    
    rotate()
  }

  const flyToCountry = (coordinates, name) => {
    if (!map.current) return

    // Animate camera to the country
    map.current.flyTo({
      center: coordinates,
      zoom: 6,
      speed: 1.2,
      curve: 1.42,
      easing: (t) => t
    })

    // Add a pulsing marker
    const marker = new mapboxgl.Marker({
      color: '#00D4FF',
      scale: 1
    })
      .setLngLat(coordinates)
      .addTo(map.current)

    // Add pulse effect
    setTimeout(() => {
      marker.remove()
    }, 3000)
  }

  if (mapError) {
    return (
      <div className="flex items-center justify-center h-full bg-surface-1">
        <div className="text-center">
          <div className="text-red-500 text-lg mb-2">
             {t('error.mapLoad')}
          </div>
          <button 
            onClick={() => window.location.reload()}
            className="btn-primary"
          >
            {t('common.refresh')}
          </button>
        </div>
      </div>
    )
  }

  return (
    <div className="relative w-full h-full">
      <AnimatePresence>
        {isMapLoading && (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            className="absolute inset-0 flex items-center justify-center bg-background/80 z-50"
          >
            <div className="text-center">
              <div className="w-16 h-16 border-4 border-primary border-t-transparent rounded-full animate-spin mx-auto mb-4"></div>
              <p className="text-text-primary font-montserrat">
                {t('common.loading')}
              </p>
            </div>
          </motion.div>
        )}
      </AnimatePresence>

      {/* Map container */}
      <div 
        ref={mapContainer}
        className="w-full h-full"
        style={{ minHeight: 'calc(100vh - 5rem)' }}
      />

      {/* Map controls overlay */}
      <div className="absolute bottom-4 left-4 z-10">
        <div className="glass rounded-lg p-3 space-y-2">
          <div className="text-sm text-text-secondary">
            {t('common.search')}: {mapCenter[1].toFixed(4)}, {mapCenter[0].toFixed(4)}
          </div>
          <div className="text-sm text-text-secondary">
            {t('common.zoom')}: {mapZoom.toFixed(1)}
          </div>
        </div>
      </div>

      {/* Map instructions */}
      {!isMapLoading && isInitialized && (
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 1 }}
          className="absolute top-4 left-4 z-10"
        >
          <div className="glass rounded-lg p-4 max-w-sm">
            <h3 className="font-montserrat font-semibold text-text-primary mb-2">
               {t('header.subtitle')}
            </h3>
            <p className="text-sm text-text-secondary">
              {t('map.instruction', 'Click on countries, zoom in to explore regions, cities, and attractions!')}
            </p>
          </div>
        </motion.div>
      )}
    </div>
  )
}

export default Map3D