// Funcin para obtener todos los pases
export const getAllCountries = () => {
  return Object.values(WORLD_COUNTRIES_DATABASE).flat();
};

// Funcin para obtener pases por continente
export const getCountriesByContinent = (continent) => {
  return WORLD_COUNTRIES_DATABASE[continent] || [];
};

// Funcin para obtener un pas por cdigo
export const getCountryByCode = (code) => {
  return getAllCountries().find(country => country.code === code);
};

// Funcin para obtener un pas por ID
export const getCountryById = (id) => {
  return getAllCountries().find(country => country.id === id);
};

