import React, { useRef, useEffect, useState } from 'react'
import { Canvas, useFrame } from '@react-three/fiber'
import { Text, useGLTF, Html } from '@react-three/drei'
import { useTranslation } from 'react-i18next'
import { motion, AnimatePresence } from 'framer-motion'
import * as THREE from 'three'
import { useAppStore } from '../store/appStore'

// 3D Avatar Component using Three.js
const Avatar3D = () => {
  const { t } = useTranslation()
  const { chatOpen, toggleChat, addChatMessage, setAiTyping, language } = useAppStore()
  const [isBlinking, setIsBlinking] = useState(false)
  const [mousePosition, setMousePosition] = useState({ x: 0, y: 0 })
  const [greetingShown, setGreetingShown] = useState(false)

  // Handle mouse movement for eye tracking
  useEffect(() => {
    const handleMouseMove = (e) => {
      setMousePosition({
        x: (e.clientX / window.innerWidth) * 2 - 1,
        y: -(e.clientY / window.innerHeight) * 2 + 1
      })
    }

    window.addEventListener('mousemove', handleMouseMove)
    return () => window.removeEventListener('mousemove', handleMouseMove)
  }, [])

  // Show initial greeting
  useEffect(() => {
    if (!greetingShown) {
      setTimeout(() => {
        addChatMessage({
          type: 'ai',
          message: t('chat.greeting'),
          delay: 1000
        })
        setGreetingShown(true)
      }, 2000)
    }
  }, [greetingShown, addChatMessage, t])

  // Random blinking animation
  useEffect(() => {
    const blinkInterval = setInterval(() => {
      setIsBlinking(true)
      setTimeout(() => setIsBlinking(false), 150)
    }, 3000 + Math.random() * 2000)

    return () => clearInterval(blinkInterval)
  }, [])

  const handleAvatarClick = () => {
    toggleChat()
    
    // Add welcome message when opening chat
    if (!chatOpen) {
      setTimeout(() => {
        setAiTyping(true)
        setTimeout(() => {
          setAiTyping(false)
          addChatMessage({
            type: 'ai',
            message: t('chat.welcome')
          })
        }, 1500)
      }, 500)
    }
  }

  return (
    <div className="avatar-3d">
      <div className="avatar-container avatar-animation">
        <Canvas
          camera={{ position: [0, 0, 3], fov: 50 }}
          style={{ background: 'transparent' }}
        >
          <ambientLight intensity={0.5} />
          <directionalLight position={[2, 2, 2]} intensity={0.8} />
          <AvatarCharacter 
            mousePosition={mousePosition}
            isBlinking={isBlinking}
            onClick={handleAvatarClick}
          />
        </Canvas>

        {/* Click hint */}
        <AnimatePresence>
          {!chatOpen && (
            <motion.div
              initial={{ opacity: 0, scale: 0.8 }}
              animate={{ opacity: 1, scale: 1 }}
              exit={{ opacity: 0, scale: 0.8 }}
              className="absolute -top-16 left-1/2 transform -translate-x-1/2 glass rounded-lg px-3 py-2 whitespace-nowrap"
            >
              <p className="text-sm text-text-primary">
                Click to chat! 
              </p>
              <div className="absolute top-full left-1/2 transform -translate-x-1/2 w-0 h-0 border-l-4 border-r-4 border-t-4 border-transparent border-t-surface-1" />
            </motion.div>
          )}
        </AnimatePresence>

        {/* Status indicator */}
        <div className="absolute -bottom-2 -right-2 w-6 h-6 bg-secondary rounded-full flex items-center justify-center">
          <div className="w-2 h-2 bg-background rounded-full animate-pulse" />
        </div>
      </div>
    </div>
  )
}

// 3D Avatar Character Component
const AvatarCharacter = ({ mousePosition, isBlinking, onClick }) => {
  const groupRef = useRef()
  const headRef = useRef()
  const leftEyeRef = useRef()
  const rightEyeRef = useRef()
  const mouthRef = useRef()

  // Rotate avatar to follow mouse
  useFrame((state, delta) => {
    if (groupRef.current) {
      // Smooth rotation based on mouse position
      const targetRotationX = mousePosition.y * 0.2
      const targetRotationY = mousePosition.x * 0.3
      
      groupRef.current.rotation.x += (targetRotationX - groupRef.current.rotation.x) * 0.05
      groupRef.current.rotation.y += (targetRotationY - groupRef.current.rotation.y) * 0.05
    }

    // Eye movement
    if (leftEyeRef.current && rightEyeRef.current) {
      const eyeTargetX = mousePosition.x * 0.3
      const eyeTargetY = -mousePosition.y * 0.3
      
      leftEyeRef.current.position.x = eyeTargetX
      leftEyeRef.current.position.y = eyeTargetY
      rightEyeRef.current.position.x = eyeTargetX
      rightEyeRef.current.position.y = eyeTargetY
    }

    // Blinking animation
    if (isBlinking && leftEyeRef.current && rightEyeRef.current) {
      const blinkScale = Math.abs(Math.sin(Date.now() * 0.05)) * 0.1
      leftEyeRef.current.scale.y = 1 - blinkScale
      rightEyeRef.current.scale.y = 1 - blinkScale
    } else {
      // Reset to normal size
      if (leftEyeRef.current && rightEyeRef.current) {
        leftEyeRef.current.scale.y = 1
        rightEyeRef.current.scale.y = 1
      }
    }
  })

  return (
    <group 
      ref={groupRef}
      onClick={onClick}
      scale={1.2}
    >
      {/* Head */}
      <mesh ref={headRef} position={[0, 0, 0]}>
        <sphereGeometry args={[0.8, 32, 32]} />
        <meshPhongMaterial 
          color="#FDBCB4" 
          shininess={30}
        />
      </mesh>

      {/* Hair */}
      <mesh position={[0, 0.6, 0.1]}>
        <sphereGeometry args={[0.85, 32, 16, 0, Math.PI * 2, 0, Math.PI / 2]} />
        <meshPhongMaterial color="#4A3728" />
      </mesh>

      {/* Eyes */}
      <group ref={leftEyeRef} position={[-0.3, 0.2, 0.7]}>
        <mesh>
          <sphereGeometry args={[0.08, 16, 16]} />
          <meshPhongMaterial color="#FFFFFF" />
        </mesh>
        <mesh position={[0, 0, 0.05]}>
          <sphereGeometry args={[0.04, 16, 16]} />
          <meshPhongMaterial color="#2C1810" />
        </mesh>
        <mesh position={[0.01, 0.01, 0.06]}>
          <sphereGeometry args={[0.02, 16, 16]} />
          <meshPhongMaterial color="#FFFFFF" />
        </mesh>
      </group>

      <group ref={rightEyeRef} position={[0.3, 0.2, 0.7]}>
        <mesh>
          <sphereGeometry args={[0.08, 16, 16]} />
          <meshPhongMaterial color="#FFFFFF" />
        </mesh>
        <mesh position={[0, 0, 0.05]}>
          <sphereGeometry args={[0.04, 16, 16]} />
          <meshPhongMaterial color="#2C1810" />
        </mesh>
        <mesh position={[-0.01, 0.01, 0.06]}>
          <sphereGeometry args={[0.02, 16, 16]} />
          <meshPhongMaterial color="#FFFFFF" />
        </mesh>
      </group>

      {/* Nose */}
      <mesh position={[0, 0, 0.8]}>
        <cylinderGeometry args={[0.02, 0.04, 0.1, 8]} />
        <meshPhongMaterial color="#FDBCB4" />
      </mesh>

      {/* Mouth */}
      <group ref={mouthRef} position={[0, -0.3, 0.75]}>
        <mesh>
          <sphereGeometry args={[0.15, 16, 8, 0, Math.PI]} />
          <meshPhongMaterial color="#2C1810" />
        </mesh>
        <mesh position={[0, 0.05, 0]}>
          <sphereGeometry args={[0.12, 16, 8, 0, Math.PI]} />
          <meshPhongMaterial color="#FDBCB4" />
        </mesh>
      </group>

      {/* Body */}
      <mesh position={[0, -1.2, 0]}>
        <cylinderGeometry args={[0.6, 0.4, 1.5, 8]} />
        <meshPhongMaterial color="#3B82F6" />
      </mesh>

      {/* Collar */}
      <mesh position={[0, -0.3, 0]}>
        <torusGeometry args={[0.25, 0.05, 8, 16]} />
        <meshPhongMaterial color="#FFFFFF" />
      </mesh>

      {/* Name tag */}
      <group position={[0, -0.8, 0.6]}>
        <mesh>
          <boxGeometry args={[0.3, 0.1, 0.02]} />
          <meshPhongMaterial color="#FFFFFF" />
        </mesh>
        <Text
          position={[0, 0, 0.02]}
          fontSize={0.05}
          color="#2C1810"
          anchorX="center"
          anchorY="middle"
        >
          Zuriel
        </Text>
      </group>

      {/* Interactive overlay */}
      <mesh 
        position={[0, 0, 1.5]} 
        onClick={onClick}
        visible={false}
      >
        <sphereGeometry args={[1.5, 16, 16]} />
        <meshBasicMaterial transparent opacity={0} />
      </mesh>
    </group>
  )
}

export default Avatar3D