import React, { useState } from 'react'
import { useTranslation } from 'react-i18next'
import { motion, AnimatePresence } from 'framer-motion'
import { 
  Heart, 
  Camera, 
  MapPin, 
  Star, 
  Download, 
  Share2,
  Eye,
  QrCode
} from 'lucide-react'
import { useAppStore } from '../store/appStore'

const DestinationCard = ({ destination, compact = false, onClick }) => {
  const { t } = useTranslation()
  const {
    likedDestinations,
    lovedDestinations,
    toggleLike,
    toggleLove,
    addUserPhoto
  } = useAppStore()

  const [showQR, setShowQR] = useState(false)
  const [imageLoaded, setImageLoaded] = useState(false)
  const [imageError, setImageError] = useState(false)

  const isLiked = likedDestinations.includes(destination.id)
  const isLoved = lovedDestinations.includes(destination.id)

  // Mock data for demo
  const mockImage = `https://images.unsplash.com/photo-1506905925346-21bda4d32df4?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=1000&q=80`
  
  const travelTips = [
    "Best time to visit: Spring (March-May) for mild weather",
    "Currency: Local currency recommended for better rates",
    "Transportation: Public transport is efficient and affordable",
    "Language: Learn basic phrases - locals appreciate the effort"
  ]

  const photoTips = [
    "Golden hour lighting (sunrise/sunset) creates warm tones",
    "Use rule of thirds for more dynamic compositions",
    "Include local people for cultural context and scale",
    "Try different angles - low and high perspectives"
  ]

  const handleLike = (e) => {
    e.stopPropagation()
    toggleLike(destination.id)
  }

  const handleLove = (e) => {
    e.stopPropagation()
    toggleLove(destination.id)
  }

  const handleViewGallery = () => {
    // Open gallery modal
    console.log('View gallery for', destination.name)
  }

  const handleAddPhoto = () => {
    setShowQR(true)
  }

  const handleQRGenerated = (qrData) => {
    // Handle QR code generation for photo upload
    console.log('QR generated:', qrData)
    setShowQR(false)
  }

  const generateQRCode = async () => {
    try {
      const QRCode = await import('qrcode')
      const qrDataURL = await QRCode.toDataURL(
        `https://vuala.world/upload?destination=${destination.id}`,
        {
          width: 200,
          margin: 2,
          color: {
            dark: '#00D4FF',
            light: '#000000'
          }
        }
      )
      handleQRGenerated(qrDataURL)
    } catch (error) {
      console.error('Error generating QR code:', error)
    }
  }

  useState(() => {
    if (showQR) {
      generateQRCode()
    }
  }, [showQR])

  if (compact) {
    return (
      <motion.div
        className="destination-card compact w-full h-48 rounded-xl overflow-hidden cursor-pointer"
        onClick={onClick}
        whileHover={{ scale: 1.05 }}
        whileTap={{ scale: 0.95 }}
      >
        <div className="relative w-full h-full">
          <img
            src={imageError ? '/placeholder-destination.jpg' : mockImage}
            alt={destination.name}
            className="w-full h-full object-cover"
            onLoad={() => setImageLoaded(true)}
            onError={() => setImageError(true)}
          />
          
          {/* Overlay */}
          <div className="absolute inset-0 bg-gradient-to-t from-black/80 via-black/20 to-transparent" />
          
          {/* Content */}
          <div className="absolute bottom-0 left-0 right-0 p-4">
            <h3 className="font-montserrat font-semibold text-text-primary text-lg mb-1">
              {destination.name}
            </h3>
            <div className="flex items-center space-x-2 text-sm text-text-secondary">
              <MapPin className="w-3 h-3" />
              <span>{destination.country || 'Beautiful Destination'}</span>
            </div>
            
            {/* Stats */}
            <div className="flex items-center space-x-4 mt-2 text-xs text-text-secondary">
              <div className="flex items-center space-x-1">
                <Heart className="w-3 h-3" />
                <span>1.2k</span>
              </div>
              <div className="flex items-center space-x-1">
                <Camera className="w-3 h-3" />
                <span>856</span>
              </div>
            </div>
          </div>
          
          {/* Interaction buttons */}
          <div className="absolute top-3 right-3 flex space-x-2">
            <button
              onClick={(e) => {
                e.stopPropagation()
                handleLike()
              }}
              className={`w-8 h-8 rounded-full flex items-center justify-center transition-all ${
                isLiked 
                  ? 'bg-primary text-background' 
                  : 'bg-black/50 text-text-primary hover:bg-primary hover:text-background'
              }`}
            >
              <Heart className={`w-4 h-4 ${isLiked ? 'fill-current' : ''}`} />
            </button>
          </div>
        </div>
      </motion.div>
    )
  }

  return (
    <motion.div
      className="destination-card w-80 h-96 rounded-xl overflow-hidden cursor-pointer"
      onClick={onClick}
      whileHover={{ 
        scale: 1.02,
        rotateY: 5,
        rotateX: 5
      }}
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
    >
      <div className="relative w-full h-full">
        <img
          src={imageError ? '/placeholder-destination.jpg' : mockImage}
          alt={destination.name}
          className="w-full h-full object-cover transition-transform duration-500 hover:scale-110"
          onLoad={() => setImageLoaded(true)}
          onError={() => setImageError(true)}
        />
        
        {/* Loading placeholder */}
        {!imageLoaded && !imageError && (
          <div className="absolute inset-0 bg-surface-2 animate-pulse" />
        )}
        
        {/* Overlay gradient */}
        <div className="absolute inset-0 bg-gradient-to-t from-black/90 via-black/40 to-transparent" />
        
        {/* Header with like/love buttons */}
        <div className="absolute top-4 right-4 flex space-x-2">
          <motion.button
            onClick={handleLike}
            className={`w-10 h-10 rounded-full flex items-center justify-center transition-all backdrop-blur-sm ${
              isLiked 
                ? 'bg-primary text-background shadow-glow-primary' 
                : 'bg-black/50 text-text-primary hover:bg-primary hover:text-background'
            }`}
            whileHover={{ scale: 1.1 }}
            whileTap={{ scale: 0.9 }}
          >
            <Heart className={`w-5 h-5 ${isLiked ? 'fill-current' : ''}`} />
          </motion.button>
          
          <motion.button
            onClick={handleLove}
            className={`w-10 h-10 rounded-full flex items-center justify-center transition-all backdrop-blur-sm ${
              isLoved 
                ? 'bg-secondary text-background shadow-glow-secondary' 
                : 'bg-black/50 text-text-primary hover:bg-secondary hover:text-background'
            }`}
            whileHover={{ scale: 1.1 }}
            whileTap={{ scale: 0.9 }}
          >
            <span className="text-lg"></span>
          </motion.button>
        </div>

        {/* Content */}
        <div className="absolute bottom-0 left-0 right-0 p-6">
          <div className="mb-4">
            <h3 className="font-montserrat font-semibold text-text-primary text-xl mb-2">
              {destination.name}
            </h3>
            <div className="flex items-center space-x-2 text-text-secondary mb-3">
              <MapPin className="w-4 h-4" />
              <span className="text-sm">{destination.country || 'Beautiful Destination'}</span>
            </div>
            
            {/* Rating */}
            <div className="flex items-center space-x-1 mb-4">
              {[1, 2, 3, 4, 5].map((star) => (
                <Star 
                  key={star} 
                  className="w-4 h-4 text-yellow-400 fill-current" 
                />
              ))}
              <span className="text-sm text-text-secondary ml-2">(4.8)</span>
            </div>
          </div>

          {/* Action buttons */}
          <div className="space-y-3">
            <button
              onClick={(e) => {
                e.stopPropagation()
                handleViewGallery()
              }}
              className="w-full flex items-center justify-center space-x-2 bg-surface-1/80 backdrop-blur-sm text-text-primary py-3 rounded-lg hover:bg-surface-1 transition-all"
            >
              <Eye className="w-4 h-4" />
              <span className="font-medium">{t('card.viewGallery')}</span>
            </button>
            
            <div className="grid grid-cols-2 gap-2">
              <button
                onClick={(e) => {
                  e.stopPropagation()
                  // Open travel tips modal
                }}
                className="flex items-center justify-center space-x-1 bg-primary/20 text-primary py-2 rounded-lg hover:bg-primary/30 transition-all"
              >
                <span></span>
                <span className="text-sm font-medium">{t('card.travelTips')}</span>
              </button>
              
              <button
                onClick={(e) => {
                  e.stopPropagation()
                  // Open photo tips modal
                }}
                className="flex items-center justify-center space-x-1 bg-secondary/20 text-secondary py-2 rounded-lg hover:bg-secondary/30 transition-all"
              >
                <Camera className="w-4 h-4" />
                <span className="text-sm font-medium">{t('card.photoTips')}</span>
              </button>
            </div>
          </div>
        </div>

        {/* Stats overlay */}
        <div className="absolute top-4 left-4">
          <div className="glass rounded-lg p-3 backdrop-blur-sm">
            <div className="flex items-center space-x-4 text-sm">
              <div className="text-center">
                <div className="font-montserrat font-semibold text-text-primary">1.2k</div>
                <div className="text-xs text-text-secondary">Likes</div>
              </div>
              <div className="w-px h-6 bg-border" />
              <div className="text-center">
                <div className="font-montserrat font-semibold text-text-primary">856</div>
                <div className="text-xs text-text-secondary">Photos</div>
              </div>
              <div className="w-px h-6 bg-border" />
              <div className="text-center">
                <div className="font-montserrat font-semibold text-text-primary">4.8</div>
                <div className="text-xs text-text-secondary">Rating</div>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* QR Code Modal */}
      <AnimatePresence>
        {showQR && (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            className="absolute inset-0 bg-black/80 flex items-center justify-center z-50"
            onClick={() => setShowQR(false)}
          >
            <motion.div
              initial={{ scale: 0.8, opacity: 0 }}
              animate={{ scale: 1, opacity: 1 }}
              exit={{ scale: 0.8, opacity: 0 }}
              className="glass rounded-xl p-8 text-center max-w-sm mx-4"
              onClick={(e) => e.stopPropagation()}
            >
              <QrCode className="w-12 h-12 text-primary mx-auto mb-4" />
              <h3 className="font-montserrat font-semibold text-text-primary mb-2">
                {t('modal.addPhoto')}
              </h3>
              <p className="text-text-secondary text-sm mb-6">
                Scan this QR code to upload your photo of {destination.name}
              </p>
              
              {/* QR Code will be generated here */}
              <div className="w-32 h-32 bg-surface-2 rounded-lg mx-auto mb-4 flex items-center justify-center">
                <QrCode className="w-16 h-16 text-text-secondary" />
              </div>
              
              <div className="flex space-x-3">
                <button
                  onClick={() => setShowQR(false)}
                  className="flex-1 bg-surface-2 text-text-primary py-2 rounded-lg hover:bg-surface-1 transition-colors"
                >
                  {t('common.close')}
                </button>
                <button
                  onClick={generateQRCode}
                  className="flex-1 bg-primary text-background py-2 rounded-lg hover:bg-primary/90 transition-colors"
                >
                  Generate
                </button>
              </div>
            </motion.div>
          </motion.div>
        )}
      </AnimatePresence>
    </motion.div>
  )
}

export default DestinationCard