import React, { useState, useRef } from 'react'
import { useTranslation } from 'react-i18next'
import { motion, AnimatePresence } from 'framer-motion'
import { 
  Globe, 
  Volume2, 
  VolumeX, 
  Settings, 
  ChevronDown,
  User,
  LogOut
} from 'lucide-react'
import { useAppStore } from '../store/appStore'

const Header = () => {
  const { t, i18n } = useTranslation()
  const {
    language,
    soundEnabled,
    isDayMode,
    admin,
    toggleSound,
    setLanguage,
    adminLogout
  } = useAppStore()

  const [showLanguageMenu, setShowLanguageMenu] = useState(false)
  const [showUserMenu, setShowUserMenu] = useState(false)
  const audioRef = useRef()

  const languages = [
    { code: 'en', name: 'English', flag: '' },
    { code: 'es', name: 'Espaol', flag: '' },
    { code: 'fr', name: 'Franais', flag: '' },
    { code: 'jp', name: '', flag: '' }
  ]

  const currentLanguage = languages.find(lang => lang.code === language) || languages[0]

  const handleLanguageChange = (langCode) => {
    setLanguage(langCode)
    i18n.changeLanguage(langCode)
    setShowLanguageMenu(false)
  }

  const handleAdminClick = () => {
    if (admin.isAuthenticated) {
      setShowUserMenu(!showUserMenu)
    } else {
      window.open('/admin', '_blank')
    }
  }

  const handleLogout = () => {
    adminLogout()
    setShowUserMenu(false)
  }

  return (
    <motion.header
      initial={{ y: -100, opacity: 0 }}
      animate={{ y: 0, opacity: 1 }}
      className="fixed top-0 left-0 right-0 z-50 glass border-b border-border"
    >
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex items-center justify-between h-20">
          {/* Logo and Title */}
          <motion.div 
            className="flex items-center space-x-3"
            whileHover={{ scale: 1.05 }}
          >
            <div className="w-10 h-10 bg-gradient-to-br from-primary to-secondary rounded-full flex items-center justify-center">
              <Globe className="w-6 h-6 text-background" />
            </div>
            <div>
              <h1 className="text-2xl font-montserrat font-bold text-text-primary">
                {t('header.title')}
              </h1>
              <p className="text-sm text-text-secondary font-raleway">
                {t('header.subtitle')}
              </p>
            </div>
          </motion.div>

          {/* Desktop Navigation */}
          <nav className="hidden md:flex items-center space-x-8">
            <a href="#explore" className="nav-item">
              {t('nav.explore')}
            </a>
            <a href="#photography" className="nav-item">
              {t('nav.photography')}
            </a>
            <a href="#guides" className="nav-item">
              {t('nav.guides')}
            </a>
            <a href="#about" className="nav-item">
              {t('nav.about')}
            </a>
          </nav>

          {/* Controls */}
          <div className="flex items-center space-x-4">
            {/* Language Selector */}
            <div className="relative">
              <motion.button
                onClick={() => setShowLanguageMenu(!showLanguageMenu)}
                className="flex items-center space-x-2 px-3 py-2 rounded-lg bg-surface-2 hover:bg-surface-1 transition-colors"
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
              >
                <span className="text-2xl">{currentLanguage.flag}</span>
                <span className="text-text-primary font-medium">{currentLanguage.code.toUpperCase()}</span>
                <ChevronDown className={`w-4 h-4 text-text-secondary transition-transform ${
                  showLanguageMenu ? 'rotate-180' : ''
                }`} />
              </motion.button>

              <AnimatePresence>
                {showLanguageMenu && (
                  <motion.div
                    initial={{ opacity: 0, y: 10, scale: 0.95 }}
                    animate={{ opacity: 1, y: 0, scale: 1 }}
                    exit={{ opacity: 0, y: 10, scale: 0.95 }}
                    className="absolute right-0 mt-2 w-48 glass rounded-lg border border-border shadow-xl"
                  >
                    {languages.map((lang) => (
                      <button
                        key={lang.code}
                        onClick={() => handleLanguageChange(lang.code)}
                        className={`w-full flex items-center space-x-3 px-4 py-3 text-left hover:bg-surface-2 transition-colors first:rounded-t-lg last:rounded-b-lg ${
                          lang.code === language ? 'bg-surface-2' : ''
                        }`}
                      >
                        <span className="text-2xl">{lang.flag}</span>
                        <span className="text-text-primary font-medium">{lang.name}</span>
                        {lang.code === language && (
                          <div className="ml-auto w-2 h-2 bg-primary rounded-full" />
                        )}
                      </button>
                    ))}
                  </motion.div>
                )}
              </AnimatePresence>
            </div>

            {/* Sound Toggle */}
            <motion.button
              onClick={toggleSound}
              className={`audio-toggle ${soundEnabled ? 'active' : ''}`}
              whileHover={{ scale: 1.1 }}
              whileTap={{ scale: 0.9 }}
              title={t('header.sound')}
            >
              {soundEnabled ? (
                <Volume2 className="w-5 h-5" />
              ) : (
                <VolumeX className="w-5 h-5" />
              )}
            </motion.button>

            {/* Admin/User Menu */}
            <div className="relative">
              <motion.button
                onClick={handleAdminClick}
                className="flex items-center space-x-2 px-3 py-2 rounded-lg bg-surface-2 hover:bg-surface-1 transition-colors"
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
              >
                <User className="w-5 h-5 text-text-primary" />
                {admin.isAuthenticated ? (
                  <span className="text-text-primary font-medium hidden sm:block">
                    Admin
                  </span>
                ) : (
                  <span className="text-text-secondary font-medium hidden sm:block">
                    {t('header.admin')}
                  </span>
                )}
                {admin.isAuthenticated && (
                  <ChevronDown className={`w-4 h-4 text-text-secondary transition-transform ${
                    showUserMenu ? 'rotate-180' : ''
                  }`} />
                )}
              </motion.button>

              <AnimatePresence>
                {showUserMenu && admin.isAuthenticated && (
                  <motion.div
                    initial={{ opacity: 0, y: 10, scale: 0.95 }}
                    animate={{ opacity: 1, y: 0, scale: 1 }}
                    exit={{ opacity: 0, y: 10, scale: 0.95 }}
                    className="absolute right-0 mt-2 w-48 glass rounded-lg border border-border shadow-xl"
                  >
                    <button
                      onClick={() => {
                        window.open('/admin', '_blank')
                        setShowUserMenu(false)
                      }}
                      className="w-full flex items-center space-x-3 px-4 py-3 text-left hover:bg-surface-2 transition-colors first:rounded-t-lg"
                    >
                      <Settings className="w-5 h-5 text-text-primary" />
                      <span className="text-text-primary font-medium">
                        {t('admin.dashboard')}
                      </span>
                    </button>
                    <button
                      onClick={handleLogout}
                      className="w-full flex items-center space-x-3 px-4 py-3 text-left hover:bg-surface-2 transition-colors last:rounded-b-lg text-red-400"
                    >
                      <LogOut className="w-5 h-5" />
                      <span className="font-medium">
                        {t('admin.logout')}
                      </span>
                    </button>
                  </motion.div>
                )}
              </AnimatePresence>
            </div>
          </div>

          {/* Mobile Menu Button */}
          <motion.button
            className="md:hidden p-2 rounded-lg bg-surface-2"
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
          >
            <div className="w-6 h-6 flex flex-col justify-center space-y-1">
              <div className="w-full h-0.5 bg-text-primary"></div>
              <div className="w-full h-0.5 bg-text-primary"></div>
              <div className="w-full h-0.5 bg-text-primary"></div>
            </div>
          </motion.button>
        </div>
      </div>

      {/* Background audio element for wind sounds */}
      <audio
        ref={audioRef}
        loop
        preload="none"
        style={{ display: 'none' }}
      >
        <source src="/audio/wind-ambient.mp3" type="audio/mpeg" />
      </audio>
    </motion.header>
  )
}

export default Header