import React, { useEffect } from 'react'
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom'
import { Toaster } from 'react-hot-toast'
import { useTranslation } from 'react-i18next'
import { motion, AnimatePresence } from 'framer-motion'

// Components
import Header from './components/Header'
import Map3D from './components/Map3D'
import SearchPanel from './components/SearchPanel'
import DestinationCard from './components/DestinationCard'
import Avatar3D from './components/Avatar3D'
import AudioController from './components/AudioController'
import WeatherWidget from './components/WeatherWidget'
import ChatInterface from './components/ChatInterface'
import AdminPanel from './admin/AdminPanel'

// Hooks
import { useAppStore } from './store/appStore'
import { useTimeSync } from './hooks/useTimeSync'
import { useWeatherSync } from './hooks/useWeatherSync'
import { useAudioInit } from './hooks/useAudioInit'

// Data
import { initializeAppData } from './data/initializeData.jsx'
import './App.css'

function App() {
  const { t, i18n } = useTranslation()
  const {
    language,
    selectedAttraction,
    destinations,
    popularDestinations,
    chatOpen,
    soundEnabled,
    isDayMode,
    weather
  } = useAppStore()

  // Custom hooks
  useTimeSync()
  useWeatherSync()
  useAudioInit()

  // Set document language
  useEffect(() => {
    document.documentElement.lang = language
  }, [language])

  // Initialize app data
  useEffect(() => {
    initializeAppData()
  }, [])

  // Keyboard shortcuts
  useEffect(() => {
    const handleKeyPress = (e) => {
      if (e.ctrlKey || e.metaKey) {
        switch (e.key) {
          case 'k':
            e.preventDefault()
            document.querySelector('.search-input')?.focus()
            break
          case '/':
            e.preventDefault()
            document.querySelector('.search-input')?.focus()
            break
          case 'm':
            e.preventDefault()
            useAppStore.getState().toggleSound()
            break
        }
      }
    }

    document.addEventListener('keydown', handleKeyPress)
    return () => document.removeEventListener('keydown', handleKeyPress)
  }, [])

  return (
    <Router>
      <div className="min-h-screen bg-background text-text-primary overflow-hidden">
        {/* Background Effects */}
        <div className="fixed inset-0 pointer-events-none">
          <div className={`absolute inset-0 transition-all duration-1000 ${
            isDayMode 
              ? 'bg-gradient-to-b from-blue-900/20 to-purple-900/20' 
              : 'bg-gradient-to-b from-indigo-900/30 to-black'
          }`} />
          
          {/* Weather Effects */}
          <AnimatePresence>
            {weather === 'rainy' && (
              <div className="absolute inset-0 weather-rain pointer-events-none" />
            )}
            {weather === 'snowy' && (
              <div className="absolute inset-0 weather-snow pointer-events-none" />
            )}
          </AnimatePresence>
          
          {/* Animated Background Particles */}
          <div className="absolute inset-0 opacity-10">
            {Array.from({ length: 50 }).map((_, i) => (
              <motion.div
                key={i}
                className="absolute w-1 h-1 bg-primary rounded-full"
                style={{
                  left: `${Math.random() * 100}%`,
                  top: `${Math.random() * 100}%`,
                }}
                animate={{
                  y: [0, -20, 0],
                  opacity: [0, 1, 0],
                }}
                transition={{
                  duration: 3 + Math.random() * 2,
                  repeat: Infinity,
                  delay: Math.random() * 2,
                }}
              />
            ))}
          </div>
        </div>

        {/* Header */}
        <Header />

        {/* Main Content */}
        <main className="relative z-10 min-h-screen pt-20">
          <div className="flex h-[calc(100vh-5rem)]">
            {/* Search Panel */}
            <div className="hidden lg:block w-96">
              <SearchPanel />
            </div>

            {/* Main Map Area */}
            <div className="flex-1 relative">
              <Map3D />
              
              {/* Floating Action Button */}
              <motion.div
                className="absolute bottom-8 left-1/2 transform -translate-x-1/2 z-20"
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
              >
                <button
                  onClick={handleRandomAdventure}
                  className="btn-primary text-lg font-montserrat px-8 py-4 rounded-full shadow-glow-primary"
                >
                  {t('main.adventure')}
                </button>
              </motion.div>

              {/* Destination Cards */}
              <AnimatePresence>
                {selectedAttraction && (
                  <motion.div
                    initial={{ opacity: 0, y: 50 }}
                    animate={{ opacity: 1, y: 0 }}
                    exit={{ opacity: 0, y: 50 }}
                    className="absolute bottom-32 right-8 z-20 max-w-sm"
                  >
                    <DestinationCard destination={selectedAttraction} />
                  </motion.div>
                )}
              </AnimatePresence>

              {/* Popular Destinations Grid */}
              {!selectedAttraction && popularDestinations.length > 0 && (
                <div className="absolute bottom-8 right-8 z-10">
                  <div className="grid grid-cols-2 gap-4 max-w-2xl">
                    {popularDestinations.slice(0, 4).map((destination, index) => (
                      <motion.div
                        key={destination.id}
                        initial={{ opacity: 0, y: 20 }}
                        animate={{ opacity: 1, y: 0 }}
                        transition={{ delay: index * 0.1 }}
                        className="w-64 h-48"
                      >
                        <DestinationCard 
                          destination={destination} 
                          compact 
                          onClick={() => {
                            // Focus on destination
                            useAppStore.getState().setMapCenter([
                              destination.coordinates.lng, 
                              destination.coordinates.lat
                            ])
                            useAppStore.getState().setSearchStep('attraction', destination)
                          }}
                        />
                      </motion.div>
                    ))}
                  </div>
                </div>
              )}
            </div>
          </div>
        </main>

        {/* Avatar 3D - Zuriel */}
        <Avatar3D />

        {/* Audio Controller */}
        <AudioController />

        {/* Weather Widget */}
        <WeatherWidget />

        {/* Chat Interface */}
        <AnimatePresence>
          {chatOpen && <ChatInterface />}
        </AnimatePresence>

        {/* Toast Notifications */}
        <Toaster
          position="top-right"
          toastOptions={{
            style: {
              background: 'var(--surface-1)',
              color: 'var(--text-primary)',
              border: '1px solid var(--border)',
            },
            success: {
              iconTheme: {
                primary: 'var(--secondary)',
                secondary: 'var(--background)',
              },
            },
            error: {
              iconTheme: {
                primary: '#ef4444',
                secondary: 'var(--background)',
              },
            },
          }}
        />

        {/* Admin Routes */}
        <Routes>
          <Route path="/admin/*" element={<AdminPanel />} />
        </Routes>
      </div>
    </Router>
  )
}

// Random Adventure Handler
const handleRandomAdventure = () => {
  const { getRandomDestination, setMapCenter, setMapZoom, setSearchStep, addChatMessage } = useAppStore.getState()
  
  const randomDestination = getRandomDestination()
  if (randomDestination) {
    setMapCenter([randomDestination.coordinates.lng, randomDestination.coordinates.lat])
    setMapZoom(8)
    setSearchStep('attraction', randomDestination)
    
    // Add a chat message about the adventure
    addChatMessage({
      type: 'system',
      message: ` ${randomDestination.name}! A random adventure awaits you there!`
    })
  }
}

export default App