import { useEffect, useRef } from 'react'
import { useAppStore } from '../store/appStore'

export const useAudioInit = () => {
  const { soundEnabled, backgroundAudio, setBackgroundAudio, setIsAudioPlaying } = useAppStore()
  const audioRef = useRef(null)

  useEffect(() => {
    // Create audio element
    if (!backgroundAudio) {
      const audio = new Audio()
      audio.loop = true
      audio.volume = 0.3
      audio.preload = 'none'
      
      // Set up audio event listeners
      audio.addEventListener('loadstart', () => {
        console.log('Audio loading started')
      })
      
      audio.addEventListener('canplaythrough', () => {
        console.log('Audio can play through')
      })
      
      audio.addEventListener('play', () => {
        setIsAudioPlaying(true)
      })
      
      audio.addEventListener('pause', () => {
        setIsAudioPlaying(false)
      })
      
      audio.addEventListener('error', (e) => {
        console.error('Audio error:', e)
        setIsAudioPlaying(false)
      })

      setBackgroundAudio(audio)
    }

    return () => {
      if (backgroundAudio) {
        backgroundAudio.pause()
        backgroundAudio.remove()
      }
    }
  }, [backgroundAudio, setBackgroundAudio, setIsAudioPlaying])

  // Handle sound toggle
  useEffect(() => {
    if (!backgroundAudio) return

    if (soundEnabled) {
      // Try to play audio (may be blocked by browser autoplay policy)
      const playPromise = backgroundAudio.play()
      if (playPromise !== undefined) {
        playPromise
          .then(() => {
            console.log('Background audio started')
          })
          .catch((error) => {
            console.log('Autoplay prevented:', error)
            setIsAudioPlaying(false)
          })
      }
    } else {
      backgroundAudio.pause()
      setIsAudioPlaying(false)
    }
  }, [soundEnabled, backgroundAudio, setIsAudioPlaying])
}