import React from 'react'
import { useTranslation } from 'react-i18next'
import { motion } from 'framer-motion'
import { Volume2, VolumeX } from 'lucide-react'
import { useAppStore } from '../store/appStore'

const AudioController = () => {
  const { t } = useTranslation()
  const { soundEnabled, isAudioPlaying, toggleSound } = useAppStore()

  return (
    <div className="audio-controller">
      <motion.button
        onClick={toggleSound}
        className={`audio-toggle ${soundEnabled ? 'active' : ''}`}
        whileHover={{ scale: 1.1 }}
        whileTap={{ scale: 0.9 }}
        title={t('header.sound')}
      >
        {soundEnabled ? (
          <Volume2 className="w-6 h-6" />
        ) : (
          <VolumeX className="w-6 h-6" />
        )}
      </motion.button>

      {/* Audio status indicator */}
      {soundEnabled && (
        <motion.div
          initial={{ opacity: 0, scale: 0.8 }}
          animate={{ opacity: 1, scale: 1 }}
          className="absolute -bottom-8 left-1/2 transform -translate-x-1/2"
        >
          <div className="glass rounded px-2 py-1 text-xs text-text-secondary whitespace-nowrap">
            {isAudioPlaying ? ' Playing' : ' Paused'}
          </div>
        </motion.div>
      )}
    </div>
  )
}

export default AudioController