import React, { useState } from 'react'
import { motion, AnimatePresence } from 'framer-motion'
import { 
  Camera, 
  Eye, 
  X, 
  Check, 
  AlertTriangle, 
  Download,
  Filter,
  Search,
  Grid,
  List
} from 'lucide-react'
import { useTranslation } from 'react-i18next'
import { useAppStore } from '../store/appStore'

const PhotoReview = () => {
  const { t } = useTranslation()
  const { admin, approvePhoto } = useAppStore()
  const [selectedPhoto, setSelectedPhoto] = useState(null)
  const [filterStatus, setFilterStatus] = useState('all')
  const [viewMode, setViewMode] = useState('grid')
  const [searchQuery, setSearchQuery] = useState('')

  // Mock pending photos data
  const [pendingPhotos] = useState([
    {
      id: 1,
      filename: 'eiffel_tower_sunset.jpg',
      user: 'john_doe',
      destination: 'Eiffel Tower, Paris',
      uploadedAt: '2024-01-15T10:30:00Z',
      status: 'pending',
      size: '2.4 MB',
      dimensions: '1920x1080',
      tags: ['sunset', 'architecture', 'landmark'],
      moderation: {
        inappropriate: false,
        copyrighted: false,
        quality: 'high'
      }
    },
    {
      id: 2,
      filename: 'tokyo_street_scene.jpg',
      user: 'sarah_travels',
      destination: 'Shibuya, Tokyo',
      uploadedAt: '2024-01-15T14:22:00Z',
      status: 'approved',
      size: '3.1 MB',
      dimensions: '2048x1536',
      tags: ['street', 'culture', 'urban'],
      moderation: {
        inappropriate: false,
        copyrighted: false,
        quality: 'high'
      }
    },
    {
      id: 3,
      filename: 'bali_beach_vacation.jpg',
      user: 'emma_smith',
      destination: 'Kuta Beach, Bali',
      uploadedAt: '2024-01-15T16:45:00Z',
      status: 'rejected',
      size: '4.2 MB',
      dimensions: '2560x1920',
      tags: ['beach', 'vacation', 'tropical'],
      moderation: {
        inappropriate: true,
        copyrighted: false,
        quality: 'medium'
      }
    }
  ])

  const getFilteredPhotos = () => {
    let filtered = pendingPhotos
    
    if (filterStatus !== 'all') {
      filtered = filtered.filter(photo => photo.status === filterStatus)
    }
    
    if (searchQuery) {
      filtered = filtered.filter(photo =>
        photo.filename.toLowerCase().includes(searchQuery.toLowerCase()) ||
        photo.destination.toLowerCase().includes(searchQuery.toLowerCase()) ||
        photo.user.toLowerCase().includes(searchQuery.toLowerCase())
      )
    }
    
    return filtered
  }

  const handleApprove = (photoId) => {
    approvePhoto(photoId)
    setSelectedPhoto(null)
  }

  const handleReject = (photoId) => {
    // In a real app, this would update the photo status
    console.log('Reject photo', photoId)
    setSelectedPhoto(null)
  }

  const getStatusColor = (status) => {
    switch (status) {
      case 'approved':
        return 'bg-green-500/20 text-green-400'
      case 'rejected':
        return 'bg-red-500/20 text-red-400'
      case 'pending':
        return 'bg-yellow-500/20 text-yellow-400'
      default:
        return 'bg-surface-2 text-text-secondary'
    }
  }

  const formatDate = (dateString) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      month: 'short',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    })
  }

  return (
    <div className="p-6 space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-montserrat font-bold text-text-primary">
            {t('admin.photos')}
          </h1>
          <p className="text-text-secondary mt-1">
            Review and moderate user-uploaded photos
          </p>
        </div>
        
        <div className="flex items-center space-x-2">
          <button
            onClick={() => setViewMode(viewMode === 'grid' ? 'list' : 'grid')}
            className="p-2 hover:bg-surface-2 rounded-lg transition-colors"
          >
            {viewMode === 'grid' ? <List className="w-5 h-5" /> : <Grid className="w-5 h-5" />}
          </button>
        </div>
      </div>

      {/* Stats */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
        <div className="glass rounded-lg p-4">
          <div className="text-2xl font-bold text-text-primary">12</div>
          <div className="text-sm text-text-secondary">Pending Review</div>
        </div>
        <div className="glass rounded-lg p-4">
          <div className="text-2xl font-bold text-green-400">48</div>
          <div className="text-sm text-text-secondary">Approved Today</div>
        </div>
        <div className="glass rounded-lg p-4">
          <div className="text-2xl font-bold text-red-400">3</div>
          <div className="text-sm text-text-secondary">Rejected</div>
        </div>
        <div className="glass rounded-lg p-4">
          <div className="text-2xl font-bold text-yellow-400">5</div>
          <div className="text-sm text-text-secondary">Flagged for Review</div>
        </div>
      </div>

      {/* Filters and Search */}
      <div className="flex items-center space-x-4">
        <div className="relative flex-1 max-w-md">
          <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 w-4 h-4 text-text-secondary" />
          <input
            type="text"
            placeholder="Search photos..."
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            className="w-full pl-10 pr-4 py-2 bg-surface-2 border border-border text-text-primary rounded-lg focus:outline-none focus:ring-2 focus:ring-primary"
          />
        </div>
        
        <select
          value={filterStatus}
          onChange={(e) => setFilterStatus(e.target.value)}
          className="px-3 py-2 bg-surface-2 border border-border text-text-primary rounded-lg focus:outline-none focus:ring-2 focus:ring-primary"
        >
          <option value="all">All Status</option>
          <option value="pending">Pending</option>
          <option value="approved">Approved</option>
          <option value="rejected">Rejected</option>
        </select>
      </div>

      {/* Photo Grid/List */}
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        className={`${
          viewMode === 'grid' 
            ? 'grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6' 
            : 'space-y-4'
        }`}
      >
        {getFilteredPhotos().map((photo) => (
          <motion.div
            key={photo.id}
            layout
            className={`glass rounded-xl border border-border overflow-hidden ${
              viewMode === 'list' ? 'flex' : ''
            }`}
          >
            {/* Photo Preview */}
            <div className={`relative ${viewMode === 'list' ? 'w-32 h-24 flex-shrink-0' : 'aspect-video'}`}>
              <div className="w-full h-full bg-gradient-to-br from-surface-2 to-surface-1 flex items-center justify-center">
                <Camera className="w-8 h-8 text-text-secondary" />
              </div>
              
              {/* Status Badge */}
              <div className="absolute top-2 right-2">
                <span className={`inline-flex items-center px-2 py-1 rounded-full text-xs font-medium ${getStatusColor(photo.status)}`}>
                  {photo.status}
                </span>
              </div>
              
              {/* Moderation Flags */}
              {photo.moderation.inappropriate && (
                <div className="absolute top-2 left-2">
                  <AlertTriangle className="w-5 h-5 text-red-400" />
                </div>
              )}
            </div>

            {/* Photo Info */}
            <div className={`p-4 ${viewMode === 'list' ? 'flex-1' : ''}`}>
              <div className="space-y-2">
                <h3 className="font-montserrat font-medium text-text-primary truncate">
                  {photo.filename}
                </h3>
                <p className="text-sm text-text-secondary truncate">
                  {photo.destination}
                </p>
                <div className="flex items-center justify-between text-xs text-text-secondary">
                  <span>by {photo.user}</span>
                  <span>{formatDate(photo.uploadedAt)}</span>
                </div>
                <div className="flex items-center space-x-4 text-xs text-text-secondary">
                  <span>{photo.dimensions}</span>
                  <span>{photo.size}</span>
                </div>
              </div>
              
              {/* Actions */}
              <div className="flex items-center space-x-2 mt-4">
                <button
                  onClick={() => setSelectedPhoto(photo)}
                  className="flex-1 flex items-center justify-center space-x-1 bg-surface-2 text-text-primary py-2 rounded-lg hover:bg-surface-1 transition-colors"
                >
                  <Eye className="w-4 h-4" />
                  <span>Review</span>
                </button>
                {photo.status === 'pending' && (
                  <>
                    <button
                      onClick={() => handleApprove(photo.id)}
                      className="p-2 bg-green-500/20 text-green-400 rounded-lg hover:bg-green-500/30 transition-colors"
                    >
                      <Check className="w-4 h-4" />
                    </button>
                    <button
                      onClick={() => handleReject(photo.id)}
                      className="p-2 bg-red-500/20 text-red-400 rounded-lg hover:bg-red-500/30 transition-colors"
                    >
                      <X className="w-4 h-4" />
                    </button>
                  </>
                )}
              </div>
            </div>
          </motion.div>
        ))}
      </motion.div>

      {/* Photo Detail Modal */}
      <AnimatePresence>
        {selectedPhoto && (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            className="fixed inset-0 bg-black/80 flex items-center justify-center z-50 p-4"
            onClick={() => setSelectedPhoto(null)}
          >
            <motion.div
              initial={{ scale: 0.8, opacity: 0 }}
              animate={{ scale: 1, opacity: 1 }}
              exit={{ scale: 0.8, opacity: 0 }}
              className="glass rounded-xl p-6 max-w-4xl w-full max-h-[90vh] overflow-y-auto"
              onClick={(e) => e.stopPropagation()}
            >
              <div className="flex items-center justify-between mb-6">
                <h2 className="text-xl font-montserrat font-bold text-text-primary">
                  Photo Review
                </h2>
                <button
                  onClick={() => setSelectedPhoto(null)}
                  className="p-2 hover:bg-surface-2 rounded-lg transition-colors"
                >
                  <X className="w-5 h-5 text-text-secondary" />
                </button>
              </div>

              <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                {/* Photo Preview */}
                <div className="aspect-video bg-surface-2 rounded-lg flex items-center justify-center">
                  <Camera className="w-16 h-16 text-text-secondary" />
                  <p className="absolute text-text-secondary mt-20">
                    Photo preview would be here
                  </p>
                </div>

                {/* Photo Details */}
                <div className="space-y-4">
                  <div>
                    <h3 className="font-montserrat font-semibold text-text-primary mb-2">
                      Details
                    </h3>
                    <div className="space-y-2 text-sm">
                      <div className="flex justify-between">
                        <span className="text-text-secondary">Filename:</span>
                        <span className="text-text-primary">{selectedPhoto.filename}</span>
                      </div>
                      <div className="flex justify-between">
                        <span className="text-text-secondary">User:</span>
                        <span className="text-text-primary">{selectedPhoto.user}</span>
                      </div>
                      <div className="flex justify-between">
                        <span className="text-text-secondary">Destination:</span>
                        <span className="text-text-primary">{selectedPhoto.destination}</span>
                      </div>
                      <div className="flex justify-between">
                        <span className="text-text-secondary">Uploaded:</span>
                        <span className="text-text-primary">{formatDate(selectedPhoto.uploadedAt)}</span>
                      </div>
                      <div className="flex justify-between">
                        <span className="text-text-secondary">Size:</span>
                        <span className="text-text-primary">{selectedPhoto.size}</span>
                      </div>
                      <div className="flex justify-between">
                        <span className="text-text-secondary">Dimensions:</span>
                        <span className="text-text-primary">{selectedPhoto.dimensions}</span>
                      </div>
                    </div>
                  </div>

                  <div>
                    <h3 className="font-montserrat font-semibold text-text-primary mb-2">
                      AI Moderation
                    </h3>
                    <div className="space-y-2 text-sm">
                      <div className="flex justify-between">
                        <span className="text-text-secondary">Inappropriate Content:</span>
                        <span className={`${selectedPhoto.moderation.inappropriate ? 'text-red-400' : 'text-green-400'}`}>
                          {selectedPhoto.moderation.inappropriate ? 'Detected' : 'Clean'}
                        </span>
                      </div>
                      <div className="flex justify-between">
                        <span className="text-text-secondary">Copyright Issues:</span>
                        <span className={`${selectedPhoto.moderation.copyrighted ? 'text-red-400' : 'text-green-400'}`}>
                          {selectedPhoto.moderation.copyrighted ? 'Detected' : 'Clear'}
                        </span>
                      </div>
                      <div className="flex justify-between">
                        <span className="text-text-secondary">Image Quality:</span>
                        <span className="text-text-primary capitalize">{selectedPhoto.moderation.quality}</span>
                      </div>
                    </div>
                  </div>

                  <div>
                    <h3 className="font-montserrat font-semibold text-text-primary mb-2">
                      Tags
                    </h3>
                    <div className="flex flex-wrap gap-2">
                      {selectedPhoto.tags.map((tag) => (
                        <span
                          key={tag}
                          className="px-2 py-1 bg-surface-2 text-text-secondary text-xs rounded"
                        >
                          {tag}
                        </span>
                      ))}
                    </div>
                  </div>

                  {selectedPhoto.status === 'pending' && (
                    <div className="flex space-x-3 pt-4">
                      <button
                        onClick={() => handleApprove(selectedPhoto.id)}
                        className="flex-1 bg-green-500 text-white py-2 rounded-lg hover:bg-green-600 transition-colors flex items-center justify-center space-x-2"
                      >
                        <Check className="w-4 h-4" />
                        <span>Approve</span>
                      </button>
                      <button
                        onClick={() => handleReject(selectedPhoto.id)}
                        className="flex-1 bg-red-500 text-white py-2 rounded-lg hover:bg-red-600 transition-colors flex items-center justify-center space-x-2"
                      >
                        <X className="w-4 h-4" />
                        <span>Reject</span>
                      </button>
                    </div>
                  )}
                </div>
              </div>
            </motion.div>
          </motion.div>
        )}
      </AnimatePresence>
    </div>
  )
}

export default PhotoReview