import React from 'react'
import { useTranslation } from 'react-i18next'
import { motion } from 'framer-motion'
import { Cloud, Sun, CloudRain, CloudSnow, Wind } from 'lucide-react'
import { useAppStore } from '../store/appStore'

const WeatherWidget = () => {
  const { t } = useTranslation()
  const { weather, weatherData } = useAppStore()

  if (!weatherData) {
    return null
  }

  const getWeatherIcon = (weatherType, icon) => {
    const iconProps = { className: "w-8 h-8" }
    
    switch (weatherType) {
      case 'sunny':
        return <Sun {...iconProps} className="w-8 h-8 text-yellow-400" />
      case 'cloudy':
        return <Cloud {...iconProps} className="w-8 h-8 text-gray-400" />
      case 'rainy':
        return <CloudRain {...iconProps} className="w-8 h-8 text-blue-400" />
      case 'snowy':
        return <CloudSnow {...iconProps} className="w-8 h-8 text-blue-200" />
      default:
        return <Sun {...iconProps} className="w-8 h-8 text-yellow-400" />
    }
  }

  return (
    <motion.div
      initial={{ opacity: 0, x: -20 }}
      animate={{ opacity: 1, x: 0 }}
      className="weather-widget"
    >
      <div className="flex items-center space-x-3">
        {getWeatherIcon(weather, weatherData.icon)}
        <div>
          <div className="weather-temp font-montserrat">
            {weatherData.temperature}C
          </div>
          <div className="weather-desc text-sm">
            {weatherData.description}
          </div>
        </div>
      </div>

      {/* Additional weather details */}
      <div className="mt-3 pt-3 border-t border-border text-xs text-text-secondary space-y-1">
        {weatherData.windspeed > 0 && (
          <div className="flex items-center space-x-1">
            <Wind className="w-3 h-3" />
            <span>{weatherData.windspeed} km/h</span>
          </div>
        )}
        
        {weatherData.precipitation > 0 && (
          <div className="text-blue-400">
             {weatherData.precipitation}mm
          </div>
        )}
      </div>

      {/* Weather-based UI effects */}
      <div className="mt-2 flex space-x-1">
        {Array.from({ length: 3 }).map((_, i) => (
          <motion.div
            key={i}
            className={`w-1 h-1 rounded-full ${
              weather === 'rainy' ? 'bg-blue-400' :
              weather === 'snowy' ? 'bg-blue-200' :
              'bg-primary'
            }`}
            animate={{
              scale: [1, 1.2, 1],
              opacity: [0.5, 1, 0.5]
            }}
            transition={{
              duration: 1,
              repeat: Infinity,
              delay: i * 0.2
            }}
          />
        ))}
      </div>
    </motion.div>
  )
}

export default WeatherWidget