import { useEffect, useState } from 'react'
import axios from 'axios'
import { useAppStore } from '../store/appStore'

export const useWeatherSync = () => {
  const { setWeather, weather } = useAppStore()
  const [location, setLocation] = useState(null)
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState(null)

  // Get user location
  const getUserLocation = () => {
    if (!navigator.geolocation) {
      setError('Geolocation is not supported by this browser')
      return
    }

    setLoading(true)
    navigator.geolocation.getCurrentPosition(
      (position) => {
        const { latitude, longitude } = position.coords
        setLocation({ lat: latitude, lng: longitude })
        setLoading(false)
      },
      (error) => {
        console.error('Error getting location:', error)
        setError('Failed to get location')
        setLoading(false)
        // Use default location (New York) if geolocation fails
        setLocation({ lat: 40.7128, lng: -74.0060 })
      },
      {
        enableHighAccuracy: true,
        timeout: 10000,
        maximumAge: 300000 // 5 minutes
      }
    )
  }

  // Fetch weather data from Open-Meteo API (free, no API key needed)
  const fetchWeatherData = async (lat, lng) => {
    try {
      const response = await axios.get('https://api.open-meteo.com/v1/forecast', {
        params: {
          latitude: lat,
          longitude: lng,
          current: 'temperature_2m,weather_code,precipitation,windspeed_10m',
          daily: 'weather_code,temperature_2m_max,temperature_2m_min,precipitation_sum',
          timezone: 'auto'
        }
      })

      const data = response.data
      if (data.current) {
        const current = data.current
        const weatherCode = current.weather_code
        const temperature = Math.round(current.temperature_2m)
        const precipitation = current.precipitation
        const windspeed = Math.round(current.windspeed_10m)

        // Convert weather code to description and icon
        const weatherInfo = getWeatherInfo(weatherCode)
        
        const weatherData = {
          temperature,
          description: weatherInfo.description,
          icon: weatherInfo.icon,
          precipitation,
          windspeed,
          rawCode: weatherCode
        }

        setWeather(weatherInfo.type, weatherData)
      }
    } catch (error) {
      console.error('Error fetching weather data:', error)
      setError('Failed to fetch weather data')
      
      // Set default weather if API fails
      setWeather('sunny', {
        temperature: 22,
        description: 'Sunny',
        icon: '',
        precipitation: 0,
        windspeed: 5
      })
    }
  }

  // Convert Open-Meteo weather codes to descriptions
  const getWeatherInfo = (code) => {
    const weatherCodes = {
      0: { description: 'Clear sky', icon: '', type: 'sunny' },
      1: { description: 'Mainly clear', icon: '', type: 'sunny' },
      2: { description: 'Partly cloudy', icon: '', type: 'cloudy' },
      3: { description: 'Overcast', icon: '', type: 'cloudy' },
      45: { description: 'Fog', icon: '', type: 'cloudy' },
      48: { description: 'Depositing rime fog', icon: '', type: 'cloudy' },
      51: { description: 'Light drizzle', icon: '', type: 'rainy' },
      53: { description: 'Moderate drizzle', icon: '', type: 'rainy' },
      55: { description: 'Dense drizzle', icon: '', type: 'rainy' },
      56: { description: 'Light freezing drizzle', icon: '', type: 'rainy' },
      57: { description: 'Dense freezing drizzle', icon: '', type: 'rainy' },
      61: { description: 'Slight rain', icon: '', type: 'rainy' },
      63: { description: 'Moderate rain', icon: '', type: 'rainy' },
      65: { description: 'Heavy rain', icon: '', type: 'rainy' },
      66: { description: 'Light freezing rain', icon: '', type: 'rainy' },
      67: { description: 'Heavy freezing rain', icon: '', type: 'rainy' },
      71: { description: 'Slight snow fall', icon: '', type: 'snowy' },
      73: { description: 'Moderate snow fall', icon: '', type: 'snowy' },
      75: { description: 'Heavy snow fall', icon: '', type: 'snowy' },
      77: { description: 'Snow grains', icon: '', type: 'snowy' },
      80: { description: 'Slight rain showers', icon: '', type: 'rainy' },
      81: { description: 'Moderate rain showers', icon: '', type: 'rainy' },
      82: { description: 'Violent rain showers', icon: '', type: 'rainy' },
      85: { description: 'Slight snow showers', icon: '', type: 'snowy' },
      86: { description: 'Heavy snow showers', icon: '', type: 'snowy' },
      95: { description: 'Thunderstorm', icon: '', type: 'rainy' },
      96: { description: 'Thunderstorm with slight hail', icon: '', type: 'rainy' },
      99: { description: 'Thunderstorm with heavy hail', icon: '', type: 'rainy' }
    }

    return weatherCodes[code] || { description: 'Unknown', icon: '', type: 'cloudy' }
  }

  // Update weather based on location
  useEffect(() => {
    if (location) {
      fetchWeatherData(location.lat, location.lng)
    }
  }, [location])

  // Get location on mount
  useEffect(() => {
    getUserLocation()
  }, [])

  // Refresh weather data every 10 minutes
  useEffect(() => {
    const interval = setInterval(() => {
      if (location) {
        fetchWeatherData(location.lat, location.lng)
      }
    }, 600000) // 10 minutes

    return () => clearInterval(interval)
  }, [location])

  return {
    location,
    loading,
    error,
    refreshWeather: () => {
      if (location) {
        fetchWeatherData(location.lat, location.lng)
      }
    }
  }
}