import React, { useEffect, useState } from 'react'
import { motion } from 'framer-motion'
import { 
  Users, 
  MapPin, 
  Camera, 
  Heart, 
  MessageSquare, 
  TrendingUp,
  Globe,
  Download,
  RefreshCw
} from 'lucide-react'
import { useTranslation } from 'react-i18next'
import { Line, Bar, Doughnut } from 'react-chartjs-2'
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  BarElement,
  Title,
  Tooltip,
  Legend,
  ArcElement,
} from 'chart.js'
import { useAppStore } from '../store/appStore'

// Register Chart.js components
ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  BarElement,
  Title,
  Tooltip,
  Legend,
  ArcElement
)

const AdminDashboard = () => {
  const { t } = useTranslation()
  const { admin, setAdminStats } = useAppStore()
  const [stats, setStats] = useState({
    totalCountries: 0,
    totalPhotos: 0,
    totalLikes: 0,
    totalQueries: 0,
    activeUsers: 0,
    topDestinations: []
  })
  const [refreshing, setRefreshing] = useState(false)

  // Mock data for demo
  useEffect(() => {
    const mockStats = {
      totalCountries: 195,
      totalPhotos: 12483,
      totalLikes: 89647,
      totalQueries: 3456,
      activeUsers: 1234,
      topDestinations: [
        { name: 'Paris, France', visits: 2341, growth: 12.5 },
        { name: 'Tokyo, Japan', visits: 1987, growth: 8.2 },
        { name: 'New York, USA', visits: 1876, growth: -2.1 },
        { name: 'London, UK', visits: 1654, growth: 5.7 },
        { name: 'Rome, Italy', visits: 1432, growth: 15.3 }
      ]
    }
    
    setStats(mockStats)
    setAdminStats(mockStats)
  }, [setAdminStats])

  const refreshStats = async () => {
    setRefreshing(true)
    // Simulate API call
    await new Promise(resolve => setTimeout(resolve, 1500))
    setRefreshing(false)
  }

  const statCards = [
    {
      title: t('stats.countriesExplored'),
      value: stats.totalCountries.toLocaleString(),
      icon: Globe,
      color: 'from-blue-500 to-blue-600',
      change: '+5.2%',
      changeType: 'positive'
    },
    {
      title: t('stats.photosUploaded'),
      value: stats.totalPhotos.toLocaleString(),
      icon: Camera,
      color: 'from-green-500 to-green-600',
      change: '+12.8%',
      changeType: 'positive'
    },
    {
      title: t('stats.likes'),
      value: stats.totalLikes.toLocaleString(),
      icon: Heart,
      color: 'from-red-500 to-red-600',
      change: '+8.4%',
      changeType: 'positive'
    },
    {
      title: t('stats.aiQueries'),
      value: stats.totalQueries.toLocaleString(),
      icon: MessageSquare,
      color: 'from-purple-500 to-purple-600',
      change: '+23.1%',
      changeType: 'positive'
    },
    {
      title: t('stats.activeUsers'),
      value: stats.activeUsers.toLocaleString(),
      icon: Users,
      color: 'from-orange-500 to-orange-600',
      change: '+3.7%',
      changeType: 'positive'
    }
  ]

  // Chart data
  const visitsData = {
    labels: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun'],
    datasets: [
      {
        label: 'Page Visits',
        data: [1200, 1900, 3000, 5000, 4200, 6800],
        borderColor: '#00D4FF',
        backgroundColor: 'rgba(0, 212, 255, 0.1)',
        tension: 0.4,
      }
    ]
  }

  const destinationData = {
    labels: stats.topDestinations.map(d => d.name),
    datasets: [
      {
        data: stats.topDestinations.map(d => d.visits),
        backgroundColor: [
          '#00D4FF',
          '#00FF7F',
          '#FF6B6B',
          '#4ECDC4',
          '#45B7D1',
        ],
        borderWidth: 0,
      }
    ]
  }

  const activityData = {
    labels: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
    datasets: [
      {
        label: 'Daily Active Users',
        data: [450, 520, 480, 650, 720, 890, 670],
        backgroundColor: '#00FF7F',
        borderRadius: 4,
      }
    ]
  }

  const chartOptions = {
    responsive: true,
    maintainAspectRatio: false,
    plugins: {
      legend: {
        display: false,
      },
    },
    scales: {
      x: {
        grid: {
          color: 'rgba(255, 255, 255, 0.1)',
        },
        ticks: {
          color: '#A1A1AA',
        },
      },
      y: {
        grid: {
          color: 'rgba(255, 255, 255, 0.1)',
        },
        ticks: {
          color: '#A1A1AA',
        },
      },
    },
  }

  return (
    <div className="p-6 space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-montserrat font-bold text-text-primary">
            {t('admin.dashboard')}
          </h1>
          <p className="text-text-secondary mt-1">
            Welcome back! Here's what's happening with Vuala.world
          </p>
        </div>
        
        <button
          onClick={refreshStats}
          disabled={refreshing}
          className="flex items-center space-x-2 bg-primary text-background px-4 py-2 rounded-lg hover:bg-primary/90 transition-colors disabled:opacity-50"
        >
          <RefreshCw className={`w-4 h-4 ${refreshing ? 'animate-spin' : ''}`} />
          <span>Refresh</span>
        </button>
      </div>

      {/* Stats Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-5 gap-6">
        {statCards.map((stat, index) => (
          <motion.div
            key={stat.title}
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: index * 0.1 }}
            className="glass rounded-xl p-6 border border-border"
          >
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm text-text-secondary mb-1">{stat.title}</p>
                <p className="text-2xl font-montserrat font-bold text-text-primary">
                  {stat.value}
                </p>
                <div className="flex items-center space-x-1 mt-2">
                  <TrendingUp className="w-3 h-3 text-green-500" />
                  <span className="text-xs text-green-500">{stat.change}</span>
                </div>
              </div>
              <div className={`w-12 h-12 bg-gradient-to-br ${stat.color} rounded-lg flex items-center justify-center`}>
                <stat.icon className="w-6 h-6 text-white" />
              </div>
            </div>
          </motion.div>
        ))}
      </div>

      {/* Charts Grid */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Visits Over Time */}
        <motion.div
          initial={{ opacity: 0, x: -20 }}
          animate={{ opacity: 1, x: 0 }}
          transition={{ delay: 0.3 }}
          className="glass rounded-xl p-6 border border-border"
        >
          <div className="flex items-center justify-between mb-4">
            <h3 className="text-lg font-montserrat font-semibold text-text-primary">
              Visits Over Time
            </h3>
            <Download className="w-5 h-5 text-text-secondary cursor-pointer hover:text-text-primary" />
          </div>
          <div className="h-64">
            <Line data={visitsData} options={chartOptions} />
          </div>
        </motion.div>

        {/* Top Destinations */}
        <motion.div
          initial={{ opacity: 0, x: 20 }}
          animate={{ opacity: 1, x: 0 }}
          transition={{ delay: 0.4 }}
          className="glass rounded-xl p-6 border border-border"
        >
          <div className="flex items-center justify-between mb-4">
            <h3 className="text-lg font-montserrat font-semibold text-text-primary">
              Top Destinations
            </h3>
            <MapPin className="w-5 h-5 text-text-secondary" />
          </div>
          <div className="h-64">
            <Doughnut 
              data={destinationData} 
              options={{
                ...chartOptions,
                plugins: {
                  legend: {
                    display: true,
                    position: 'bottom',
                    labels: {
                      color: '#A1A1AA',
                      padding: 20,
                      usePointStyle: true,
                    },
                  },
                },
              }} 
            />
          </div>
        </motion.div>
      </div>

      {/* Activity and Top Destinations Table */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Daily Activity */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.5 }}
          className="glass rounded-xl p-6 border border-border"
        >
          <div className="flex items-center justify-between mb-4">
            <h3 className="text-lg font-montserrat font-semibold text-text-primary">
              Daily Active Users
            </h3>
            <Users className="w-5 h-5 text-text-secondary" />
          </div>
          <div className="h-64">
            <Bar data={activityData} options={chartOptions} />
          </div>
        </motion.div>

        {/* Top Destinations Table */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.6 }}
          className="glass rounded-xl p-6 border border-border"
        >
          <div className="flex items-center justify-between mb-4">
            <h3 className="text-lg font-montserrat font-semibold text-text-primary">
              Most Visited Destinations
            </h3>
            <TrendingUp className="w-5 h-5 text-text-secondary" />
          </div>
          <div className="space-y-4">
            {stats.topDestinations.map((destination, index) => (
              <div key={destination.name} className="flex items-center justify-between">
                <div className="flex items-center space-x-3">
                  <div className={`w-8 h-8 rounded-full flex items-center justify-center text-sm font-bold text-background ${
                    index === 0 ? 'bg-yellow-500' :
                    index === 1 ? 'bg-gray-400' :
                    index === 2 ? 'bg-orange-600' :
                    'bg-primary'
                  }`}>
                    {index + 1}
                  </div>
                  <span className="font-medium text-text-primary">
                    {destination.name}
                  </span>
                </div>
                <div className="text-right">
                  <div className="text-sm font-medium text-text-primary">
                    {destination.visits.toLocaleString()} visits
                  </div>
                  <div className={`text-xs ${
                    destination.growth > 0 ? 'text-green-500' : 'text-red-500'
                  }`}>
                    {destination.growth > 0 ? '+' : ''}{destination.growth}%
                  </div>
                </div>
              </div>
            ))}
          </div>
        </motion.div>
      </div>

      {/* Recent Activity */}
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ delay: 0.7 }}
        className="glass rounded-xl p-6 border border-border"
      >
        <div className="flex items-center justify-between mb-4">
          <h3 className="text-lg font-montserrat font-semibold text-text-primary">
            Recent Activity
          </h3>
          <button className="text-sm text-primary hover:text-primary/80">
            View All
          </button>
        </div>
        <div className="space-y-4">
          {[
            { type: 'photo', user: 'John D.', action: 'uploaded a photo', target: 'Eiffel Tower', time: '2 minutes ago' },
            { type: 'like', user: 'Sarah M.', action: 'liked', target: 'Machu Picchu', time: '5 minutes ago' },
            { type: 'chat', user: 'Mike R.', action: 'asked AI about', target: 'travel tips for Tokyo', time: '10 minutes ago' },
            { type: 'visit', user: 'Emma L.', action: 'visited', target: 'Santorini, Greece', time: '15 minutes ago' },
            { type: 'photo', user: 'Alex K.', action: 'uploaded a photo', target: 'Statue of Liberty', time: '20 minutes ago' }
          ].map((activity, index) => (
            <div key={index} className="flex items-center space-x-3 p-3 bg-surface-2/50 rounded-lg">
              <div className={`w-8 h-8 rounded-full flex items-center justify-center ${
                activity.type === 'photo' ? 'bg-green-500' :
                activity.type === 'like' ? 'bg-red-500' :
                activity.type === 'chat' ? 'bg-purple-500' :
                'bg-blue-500'
              }`}>
                {activity.type === 'photo' && <Camera className="w-4 h-4 text-white" />}
                {activity.type === 'like' && <Heart className="w-4 h-4 text-white" />}
                {activity.type === 'chat' && <MessageSquare className="w-4 h-4 text-white" />}
                {activity.type === 'visit' && <MapPin className="w-4 h-4 text-white" />}
              </div>
              <div className="flex-1">
                <span className="text-text-primary font-medium">{activity.user}</span>
                <span className="text-text-secondary"> {activity.action} </span>
                <span className="text-primary font-medium">{activity.target}</span>
              </div>
              <div className="text-xs text-text-secondary">
                {activity.time}
              </div>
            </div>
          ))}
        </div>
      </motion.div>
    </div>
  )
}

export default AdminDashboard