// Massive World Data Initialization
// Complete global database with 195+ countries and thousands of attractions
import { getAllCountries } from './worldCountriesDatabase.js';
import { getAttractionsByCountry } from './worldEmblematicAttractions.js';

export const initializeAppData = async () => {
  try {
    console.log(' Initializing World Database...');
    
    // Get all countries from our world database
    const allCountries = getAllCountries();
    console.log(' Total countries loaded:', allCountries.length);
    
    // Get all attractions for countries we have detailed data for
    const countriesWithAttractions = [
      'US', 'FR', 'JP', 'ES', 'IT', 'GB', 'AU', 'CA', 'GR', 'PE', 
      'AR', 'BR', 'DE', 'EG', 'MX', 'TH', 'CN', 'IN', 'NG', 'ZA'
    ];
    
    const countries = [];
    
    // Process countries with detailed attraction data first
    for (const countryCode of countriesWithAttractions) {
      const countryInfo = allCountries.find(c => c.code === countryCode);
      if (countryInfo) {
        const attractionData = getAttractionsByCountry(countryCode);
        if (attractionData.regions && Object.keys(attractionData.regions).length > 0) {
          const country = {
            id: countryInfo.id,
            name: countryInfo.name,
            code: countryInfo.code,
            flag: countryInfo.flag,
            coordinates: countryInfo.coordinates,
            regions: []
          };
          
          // Add regions and attractions
          for (const [regionName, regionData] of Object.entries(attractionData.regions)) {
            const region = {
              id: country.regions.length * 100 + country.regions.length + 1,
              name: regionName,
              countryId: country.id,
              cities: []
            };
            
            for (const [cityName, cityData] of Object.entries(regionData.cities)) {
              const city = {
                id: region.cities.length * 1000 + region.cities.length + 1,
                name: cityName,
                regionId: region.id,
                attractions: []
              };
              
              for (const attraction of cityData.attractions) {
                const attractionFormatted = {
                  id: attraction.id,
                  name: attraction.name,
                  type: attraction.type,
                  coordinates: attraction.coordinates,
                  description: attraction.review?.summary || 'Iconic destination with rich history and cultural significance.',
                  travelTips: attraction.travel_tips || [
                    'Research local customs and traditions',
                    'Book accommodations well in advance',
                    'Bring comfortable walking shoes',
                    'Allow extra time for special experiences',
                    'Respect local environment and wildlife'
                  ],
                  photoTips: attraction.photo_tips || [
                    'Golden hour provides the best natural lighting',
                    'Use wide-angle lens for architectural shots',
                    'Include local people for cultural context',
                    'Try different perspectives and angles',
                    'Respect photography restrictions at sacred sites'
                  ]
                };
                city.attractions.push(attractionFormatted);
              }
              
              region.cities.push(city);
            }
            
            country.regions.push(region);
          }
          
          countries.push(country);
          console.log(` ${country.name} loaded with ${country.regions.reduce((total, r) => total + r.cities.reduce((cityTotal, c) => cityTotal + c.attractions.length, 0), 0)} attractions`);
        }
      }
    }
    
    // Add remaining countries with basic information
    for (const countryInfo of allCountries) {
      if (!countriesWithAttractions.includes(countryInfo.code)) {
        countries.push({
          id: countryInfo.id,
          name: countryInfo.name,
          code: countryInfo.code,
          flag: countryInfo.flag,
          coordinates: countryInfo.coordinates,
          regions: [
            {
              id: countryInfo.id * 100,
              name: 'Main Region',
              countryId: countryInfo.id,
              cities: [
                {
                  id: countryInfo.id * 1000,
                  name: countryInfo.name,
                  regionId: countryInfo.id * 100,
                  attractions: [
                    {
                      id: countryInfo.id * 10000,
                      name: `${countryInfo.name} - Capital City`,
                      type: 'capital',
                      coordinates: countryInfo.coordinates,
                      description: `The capital and most important city of ${countryInfo.name}, representing the heart of the country's culture, politics, and history.`,
                      travelTips: [
                        'Explore the historic city center and landmarks',
                        'Try local cuisine at traditional restaurants',
                        'Visit museums and cultural sites',
                        'Experience local festivals and events',
                        'Learn basic phrases in the local language'
                      ],
                      photoTips: [
                        'Capture architecture and cityscapes',
                        'Document local culture and daily life',
                        'Use natural light for authentic photos',
                        'Include local landmarks and monuments',
                        "Respect people's privacy when photographing"
                      ]
                    },
                    {
                      id: countryInfo.id * 10001,
                      name: `${countryInfo.name} - National Park`,
                      type: 'natural',
                      coordinates: {
                        lat: countryInfo.coordinates.lat + (Math.random() - 0.5) * 2,
                        lng: countryInfo.coordinates.lng + (Math.random() - 0.5) * 2
                      },
                      description: `Beautiful natural landscape showcasing the diverse geography and wildlife of ${countryInfo.name}.`,
                      travelTips: [
                        'Respect wildlife and stay on designated paths',
                        'Bring appropriate clothing for weather conditions',
                        'Pack water and snacks for long hikes',
                        'Check park hours and regulations beforehand',
                        'Consider hiring local guides for better experience'
                      ],
                      photoTips: [
                        'Use telephoto lens for wildlife photography',
                        'Golden hour enhances natural landscapes',
                        'Include water features for dynamic compositions',
                        'Focus on endemic species and unique flora',
                        'Practice responsible photography ethics'
                      ]
                    },
                    {
                      id: countryInfo.id * 10002,
                      name: `${countryInfo.name} - Cultural Heritage Site`,
                      type: 'heritage',
                      coordinates: {
                        lat: countryInfo.coordinates.lat + (Math.random() - 0.5) * 1.5,
                        lng: countryInfo.coordinates.lng + (Math.random() - 0.5) * 1.5
                      },
                      description: `Historic site of cultural and architectural significance, representing ${countryInfo.name}'s rich heritage and traditions.`,
                      travelTips: [
                        "Learn about the site's historical significance",
                        'Respect cultural and religious customs',
                        'Dress appropriately for the location',
                        'Allow time for proper exploration and reflection',
                        'Support local conservation efforts'
                      ],
                      photoTips: [
                        'Capture architectural details and craftsmanship',
                        'Document cultural ceremonies if appropriate',
                        'Use available light for authentic atmosphere',
                        'Include descriptive signage and information',
                        'Respect photography restrictions at sacred sites'
                      ]
                    }
                  ]
                }
              ]
            }
          ]
        });
      }
    }

    console.log(` Total countries: ${countries.length}`);
    
    // Popular destinations (flatten from countries data)
    const popularDestinations = [];
    countries.forEach(country => {
      country.regions.forEach(region => {
        region.cities.forEach(city => {
          city.attractions.forEach(attraction => {
            popularDestinations.push({
              ...attraction,
              country: country.name,
              region: region.name,
              city: city.name,
              flag: country.flag,
              rating: 4.2 + Math.random() * 0.8, // Random rating 4.2-5.0
              likes: Math.floor(Math.random() * 2000) + 500,
              photos: Math.floor(Math.random() * 1500) + 200
            });
          });
        });
      });
    });

    // Update store
    const { setCountries, setRegions, setCities, setAttractions, setDestinations, setPopularDestinations } = useAppStore.getState();
    
    setCountries(countries);
    setRegions(countries.flatMap(c => c.regions));
    setCities(countries.flatMap(c => c.regions.flatMap(r => r.cities)));
    setAttractions(countries.flatMap(c => c.regions.flatMap(r => r.cities.flatMap(city => city.attractions))));
    setDestinations(popularDestinations);
    setPopularDestinations(popularDestinations.slice(0, 12));

    console.log(' World data initialized successfully');
    console.log(` Summary: ${countries.length} countries, ${popularDestinations.length} total attractions`);
  } catch (error) {
    console.error('Error initializing world data:', error);
  }
}

// Import the store hook (this should be imported at the top of the file where used)
let useAppStore;
if (typeof window !== 'undefined') {
  useAppStore = require('../store/appStore.js').useAppStore;
}

