import React, { useState, useRef, useEffect } from 'react'
import { useTranslation } from 'react-i18next'
import { motion, AnimatePresence } from 'framer-motion'
import { 
  Send, 
  X, 
  Bot, 
  User, 
  Minimize2, 
  Maximize2,
  MessageCircle
} from 'lucide-react'
import { useAppStore } from '../store/appStore'

const ChatInterface = () => {
  const { t } = useTranslation()
  const {
    chatOpen,
    chatMessages,
    aiTyping,
    toggleChat,
    addChatMessage,
    setAiTyping,
    clearChat
  } = useAppStore()
  
  const [inputMessage, setInputMessage] = useState('')
  const [isMinimized, setIsMinimized] = useState(false)
  const messagesEndRef = useRef(null)
  const inputRef = useRef(null)

  // Auto-scroll to bottom when new messages arrive
  useEffect(() => {
    if (messagesEndRef.current) {
      messagesEndRef.current.scrollIntoView({ behavior: 'smooth' })
    }
  }, [chatMessages, aiTyping])

  // Focus input when chat opens
  useEffect(() => {
    if (chatOpen && !isMinimized) {
      setTimeout(() => {
        inputRef.current?.focus()
      }, 300)
    }
  }, [chatOpen, isMinimized])

  // Mock AI response function
  const getAIResponse = (message) => {
    const responses = {
      en: [
        "That's a great question! Based on your location, I'd recommend visiting local cultural sites and trying authentic regional cuisine.",
        "For the best photography, I suggest visiting during golden hour and exploring hidden gems away from tourist crowds.",
        "Here's a travel tip: Always research local customs and learn a few basic phrases in the local language - it goes a long way!",
        "I love helping with travel planning! What type of experience are you looking for - adventure, culture, relaxation, or something else?",
        "Don't forget to try the local specialties and interact with locals. The best travel memories often come from unexpected encounters!"
      ],
      es: [
        "Excelente pregunta! Basndome en tu ubicacin, te recomiendo visitar sitios culturales locales y probar la gastronoma regional autntica.",
        "Para la mejor fotografa, sugiero visitar durante la hora dorada y explorar gemas ocultas lejos de las multitudes tursticas.",
        "Consejo de viaje: Siempre investiga las costumbres locales y aprende algunas frases bsicas en el idioma local - vale mucho la pena!",
        "Me encanta ayudar con la planificacin de viajes! Qu tipo de experiencia buscas - aventura, cultura, relajacin u otra cosa?",
        "No olvides probar las especialidades locales e interactuar con los locales. Los mejores recuerdos de viaje a menudo vienen de encuentros inesperados!"
      ],
      fr: [
        "Excellente question ! Bas sur votre localisation, je recommande de visiter des sites culturels locaux et d'essayer la cuisine rgionale authentique.",
        "Pour la meilleure photographie, je suggre de visiter pendant l'heure dore et d'explorer des trsors cachs loin des foules touristiques.",
        "Astuce de voyage : Recherchez toujours les coutumes locales et apprenez quelques phrases de base dans la langue locale - cela fait toute la diffrence !",
        "J'adore aider avec la planification de voyage ! Quel type d'exprience recherchez-vous - aventure, culture, dtente ou autre chose ?",
        "N'oubliez pas d'essayer les spcialits locales et d'interagir avec les locaux. Les meilleurs souvenirs de voyage viennent souvent de rencontres inattendues !"
      ],
      jp: [
        "",
        "",
        " - ",
        "urricular - ",
        ""
      ]
    }

    const currentLang = useAppStore.getState().language
    const langResponses = responses[currentLang] || responses.en
    return langResponses[Math.floor(Math.random() * langResponses.length)]
  }

  const handleSendMessage = async () => {
    if (!inputMessage.trim()) return

    // Add user message
    addChatMessage({
      type: 'user',
      message: inputMessage
    })

    const userMessage = inputMessage
    setInputMessage('')
    setAiTyping(true)

    // Simulate AI thinking time
    setTimeout(() => {
      setAiTyping(false)
      
      // Add AI response
      const aiResponse = getAIResponse(userMessage)
      addChatMessage({
        type: 'ai',
        message: aiResponse
      })
    }, 1000 + Math.random() * 2000)
  }

  const handleKeyPress = (e) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault()
      handleSendMessage()
    }
  }

  if (!chatOpen) return null

  return (
    <motion.div
      initial={{ opacity: 0, scale: 0.8, y: 20 }}
      animate={{ opacity: 1, scale: 1, y: 0 }}
      exit={{ opacity: 0, scale: 0.8, y: 20 }}
      className={`fixed bottom-40 right-6 z-50 transition-all duration-300 ${
        isMinimized ? 'w-80 h-16' : 'w-96 h-[500px]'
      }`}
    >
      <div className="glass rounded-xl border border-border shadow-2xl overflow-hidden">
        {/* Header */}
        <div className="flex items-center justify-between p-4 border-b border-border bg-surface-1/50">
          <div className="flex items-center space-x-3">
            <div className="w-8 h-8 bg-gradient-to-br from-primary to-secondary rounded-full flex items-center justify-center">
              <Bot className="w-5 h-5 text-background" />
            </div>
            <div>
              <h3 className="font-montserrat font-semibold text-text-primary">
                Zuriel
              </h3>
              <p className="text-xs text-text-secondary">
                {aiTyping ? 'Typing...' : 'AI Travel Assistant'}
              </p>
            </div>
          </div>
          
          <div className="flex items-center space-x-2">
            <button
              onClick={() => setIsMinimized(!isMinimized)}
              className="p-1 hover:bg-surface-2 rounded transition-colors"
            >
              {isMinimized ? (
                <Maximize2 className="w-4 h-4 text-text-secondary" />
              ) : (
                <Minimize2 className="w-4 h-4 text-text-secondary" />
              )}
            </button>
            <button
              onClick={toggleChat}
              className="p-1 hover:bg-surface-2 rounded transition-colors"
            >
              <X className="w-4 h-4 text-text-secondary" />
            </button>
          </div>
        </div>

        {!isMinimized && (
          <>
            {/* Messages */}
            <div className="flex-1 p-4 space-y-4 max-h-80 overflow-y-auto custom-scrollbar">
              <AnimatePresence>
                {chatMessages.length === 0 ? (
                  <div className="text-center py-8">
                    <MessageCircle className="w-12 h-12 text-text-secondary mx-auto mb-3" />
                    <p className="text-text-secondary">
                      {t('chat.welcome')}
                    </p>
                  </div>
                ) : (
                  chatMessages.map((message, index) => (
                    <motion.div
                      key={index}
                      initial={{ opacity: 0, y: 10 }}
                      animate={{ opacity: 1, y: 0 }}
                      className={`flex ${message.type === 'user' ? 'justify-end' : 'justify-start'}`}
                    >
                      <div className={`flex items-start space-x-2 max-w-[80%] ${
                        message.type === 'user' ? 'flex-row-reverse space-x-reverse' : ''
                      }`}>
                        <div className={`w-6 h-6 rounded-full flex items-center justify-center flex-shrink-0 ${
                          message.type === 'user' 
                            ? 'bg-primary text-background' 
                            : 'bg-surface-2 text-text-primary'
                        }`}>
                          {message.type === 'user' ? (
                            <User className="w-3 h-3" />
                          ) : (
                            <Bot className="w-3 h-3" />
                          )}
                        </div>
                        
                        <div className={`rounded-lg px-3 py-2 ${
                          message.type === 'user'
                            ? 'bg-primary text-background'
                            : 'bg-surface-2 text-text-primary'
                        }`}>
                          <p className="text-sm">{message.message}</p>
                        </div>
                      </div>
                    </motion.div>
                  ))
                )}
              </AnimatePresence>

              {/* AI Typing Indicator */}
              <AnimatePresence>
                {aiTyping && (
                  <motion.div
                    initial={{ opacity: 0, y: 10 }}
                    animate={{ opacity: 1, y: 0 }}
                    exit={{ opacity: 0, y: -10 }}
                    className="flex justify-start"
                  >
                    <div className="flex items-center space-x-2">
                      <div className="w-6 h-6 rounded-full bg-surface-2 flex items-center justify-center">
                        <Bot className="w-3 h-3 text-text-primary" />
                      </div>
                      <div className="bg-surface-2 rounded-lg px-3 py-2">
                        <div className="flex space-x-1">
                          {[0, 1, 2].map((i) => (
                            <motion.div
                              key={i}
                              className="w-1 h-1 bg-text-secondary rounded-full"
                              animate={{
                                scale: [1, 1.5, 1],
                                opacity: [0.5, 1, 0.5]
                              }}
                              transition={{
                                duration: 0.6,
                                repeat: Infinity,
                                delay: i * 0.2
                              }}
                            />
                          ))}
                        </div>
                      </div>
                    </div>
                  </motion.div>
                )}
              </AnimatePresence>

              <div ref={messagesEndRef} />
            </div>

            {/* Input */}
            <div className="border-t border-border p-4">
              <div className="flex space-x-2">
                <input
                  ref={inputRef}
                  type="text"
                  value={inputMessage}
                  onChange={(e) => setInputMessage(e.target.value)}
                  onKeyPress={handleKeyPress}
                  placeholder={t('chat.placeholder')}
                  className="flex-1 bg-surface-2 border border-border text-text-primary rounded-lg px-3 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-primary focus:border-transparent"
                  disabled={aiTyping}
                />
                <button
                  onClick={handleSendMessage}
                  disabled={!inputMessage.trim() || aiTyping}
                  className="bg-primary text-background p-2 rounded-lg hover:bg-primary/90 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
                >
                  <Send className="w-4 h-4" />
                </button>
              </div>
            </div>
          </>
        )}
      </div>
    </motion.div>
  )
}

export default ChatInterface