import { create } from 'zustand'
import { persist } from 'zustand/middleware'

export const useAppStore = create(
  persist(
    (set, get) => ({
      // UI State
      language: 'en',
      soundEnabled: false,
      currentTime: new Date().toHours(),
      isDayMode: true,
      weather: 'sunny',
      weatherData: null,
      
      // Map State
      mapCenter: [-74.006, 40.7128], // New York
      mapZoom: 2,
      isMapLoading: false,
      mapError: null,
      
      // Search State
      searchQuery: '',
      searchResults: [],
      searchHistory: [],
      selectedCountry: null,
      selectedRegion: null,
      selectedCity: null,
      selectedAttraction: null,
      searchSteps: {
        country: null,
        region: null,
        city: null,
        attraction: null
      },
      
      // Data State
      countries: [],
      regions: [],
      cities: [],
      attractions: [],
      destinations: [],
      popularDestinations: [],
      
      // User Interaction
      likedDestinations: [],
      lovedDestinations: [],
      userPhotos: [],
      userReviews: [],
      
      // Chat & AI
      chatOpen: false,
      chatMessages: [],
      aiTyping: false,
      
      // Admin State (for admin panel)
      admin: {
        isAuthenticated: false,
        user: null,
        stats: {
          totalCountries: 0,
          totalPhotos: 0,
          totalLikes: 0,
          totalQueries: 0,
          activeUsers: 0,
          topDestinations: []
        },
        pendingPhotos: [],
        recentActivity: []
      },
      
      // Audio State
      backgroundAudio: null,
      audioVolume: 0.3,
      isAudioPlaying: false,
      
      // Actions
      setLanguage: (lang) => set({ language: lang }),
      toggleSound: () => set((state) => ({ 
        soundEnabled: !state.soundEnabled,
        isAudioPlaying: !state.soundEnabled ? false : state.isAudioPlaying
      })),
      setCurrentTime: (time) => {
        const isDay = time >= 6 && time < 18
        set({ currentTime: time, isDayMode: isDay })
      },
      setWeather: (weather, data = null) => set({ weather, weatherData: data }),
      
      // Map Actions
      setMapCenter: (center) => set({ mapCenter: center }),
      setMapZoom: (zoom) => set({ mapZoom: zoom }),
      setMapLoading: (loading) => set({ isMapLoading: loading }),
      setMapError: (error) => set({ mapError: error }),
      
      // Search Actions
      setSearchQuery: (query) => set({ searchQuery: query }),
      setSearchResults: (results) => set({ searchResults: results }),
      addToSearchHistory: (query) => {
        const history = get().searchHistory
        if (!history.includes(query)) {
          set({ searchHistory: [query, ...history].slice(0, 10) })
        }
      },
      setSearchStep: (step, value) => set((state) => ({
        searchSteps: { ...state.searchSteps, [step]: value }
      })),
      resetSearch: () => set({
        searchQuery: '',
        searchResults: [],
        selectedCountry: null,
        selectedRegion: null,
        selectedCity: null,
        selectedAttraction: null,
        searchSteps: { country: null, region: null, city: null, attraction: null }
      }),
      
      // Data Actions
      setCountries: (countries) => set({ countries }),
      setRegions: (regions) => set({ regions }),
      setCities: (cities) => set({ cities }),
      setAttractions: (attractions) => set({ attractions }),
      setDestinations: (destinations) => set({ destinations }),
      setPopularDestinations: (destinations) => set({ popularDestinations: destinations }),
      
      // User Actions
      toggleLike: (destinationId) => set((state) => {
        const liked = state.likedDestinations
        const loved = state.lovedDestinations
        
        // Remove from love if exists
        const newLoved = loved.filter(id => id !== destinationId)
        
        // Toggle like
        const newLiked = liked.includes(destinationId)
          ? liked.filter(id => id !== destinationId)
          : [...liked, destinationId]
          
        return { likedDestinations: newLiked, lovedDestinations: newLoved }
      }),
      
      toggleLove: (destinationId) => set((state) => {
        const liked = state.likedDestinations
        const loved = state.lovedDestinations
        
        // Remove from like if exists
        const newLiked = liked.filter(id => id !== destinationId)
        
        // Toggle love
        const newLoved = loved.includes(destinationId)
          ? loved.filter(id => id !== destinationId)
          : [...loved, destinationId]
          
        return { likedDestinations: newLiked, lovedDestinations: newLoved }
      }),
      
      addUserPhoto: (photo) => set((state) => ({
        userPhotos: [...state.userPhotos, { ...photo, id: Date.now() }]
      })),
      
      // Chat Actions
      toggleChat: () => set((state) => ({ chatOpen: !state.chatOpen })),
      addChatMessage: (message) => set((state) => ({
        chatMessages: [...state.chatMessages, {
          ...message,
          id: Date.now(),
          timestamp: new Date()
        }]
      })),
      setAiTyping: (typing) => set({ aiTyping: typing }),
      clearChat: () => set({ chatMessages: [] }),
      
      // Audio Actions
      setBackgroundAudio: (audio) => set({ backgroundAudio: audio }),
      setAudioVolume: (volume) => set({ audioVolume: volume }),
      setIsAudioPlaying: (playing) => set({ isAudioPlaying: playing }),
      
      // Admin Actions
      adminLogin: (username, password) => {
        if (username === 'admin@vuala.world' && password === 'Vuala2025!') {
          set((state) => ({
            admin: { ...state.admin, isAuthenticated: true, user: { username } }
          }))
          return true
        }
        return false
      },
      
      adminLogout: () => set((state) => ({
        admin: { 
          ...state.admin, 
          isAuthenticated: false, 
          user: null 
        }
      })),
      
      setAdminStats: (stats) => set((state) => ({
        admin: { ...state.admin, stats }
      })),
      
      setPendingPhotos: (photos) => set((state) => ({
        admin: { ...state.admin, pendingPhotos: photos }
      })),
      
      approvePhoto: (photoId) => set((state) => ({
        admin: {
          ...state.admin,
          pendingPhotos: state.admin.pendingPhotos.filter(p => p.id !== photoId)
        }
      })),
      
      // Random Adventure
      getRandomDestination: () => {
        const destinations = get().destinations
        return destinations[Math.floor(Math.random() * destinations.length)]
      }
    }),
    {
      name: 'vuala-storage',
      partialize: (state) => ({
        language: state.language,
        searchHistory: state.searchHistory,
        likedDestinations: state.likedDestinations,
        lovedDestinations: state.lovedDestinations,
        userPhotos: state.userPhotos,
        admin: state.admin
      })
    }
  )
)