import React, { useState } from 'react'
import { Routes, Route, Navigate } from 'react-router-dom'
import { motion, AnimatePresence } from 'framer-motion'
import { 
  LayoutDashboard, 
  MapPin, 
  Camera, 
  FileText, 
  Users, 
  Settings, 
  LogOut,
  Menu,
  X
} from 'lucide-react'
import { useTranslation } from 'react-i18next'
import { useAppStore } from '../store/appStore'

// Admin Components
import AdminLogin from './AdminLogin'
import AdminDashboard from './AdminDashboard'
import ContentManagement from './ContentManagement'
import PhotoReview from './PhotoReview'
import ReportsPanel from './ReportsPanel'
import UserManagement from './UserManagement'

const AdminPanel = () => {
  const { t } = useTranslation()
  const { admin, adminLogout } = useAppStore()
  const [sidebarOpen, setSidebarOpen] = useState(false)

  if (!admin.isAuthenticated) {
    return <AdminLogin />
  }

  const navigationItems = [
    {
      name: t('admin.dashboard'),
      href: '/admin/dashboard',
      icon: LayoutDashboard,
      current: true
    },
    {
      name: t('admin.content'),
      href: '/admin/content',
      icon: MapPin,
      current: false
    },
    {
      name: t('admin.photos'),
      href: '/admin/photos',
      icon: Camera,
      current: false
    },
    {
      name: t('admin.reports'),
      href: '/admin/reports',
      icon: FileText,
      current: false
    },
    {
      name: t('admin.users'),
      href: '/admin/users',
      icon: Users,
      current: false
    }
  ]

  const handleLogout = () => {
    adminLogout()
  }

  return (
    <div className="min-h-screen bg-background">
      {/* Sidebar for desktop */}
      <div className="hidden lg:flex lg:flex-shrink-0">
        <div className="admin-sidebar">
          <div className="flex h-full flex-col">
            {/* Logo */}
            <div className="flex h-20 items-center px-6 border-b border-border">
              <div className="flex items-center space-x-3">
                <div className="w-8 h-8 bg-gradient-to-br from-primary to-secondary rounded-full flex items-center justify-center">
                  <span className="text-sm font-bold text-background">V</span>
                </div>
                <div>
                  <h1 className="font-montserrat font-bold text-text-primary">
                    Vuala Admin
                  </h1>
                  <p className="text-xs text-text-secondary">
                    {t('admin.panel')}
                  </p>
                </div>
              </div>
            </div>

            {/* Navigation */}
            <nav className="flex-1 px-4 py-6 space-y-2">
              {navigationItems.map((item) => (
                <a
                  key={item.name}
                  href={item.href}
                  className={`admin-nav-item group flex items-center px-3 py-3 text-sm font-medium rounded-lg transition-colors ${
                    item.current
                      ? 'bg-primary/10 text-primary border-l-4 border-primary'
                      : 'text-text-secondary hover:text-primary hover:bg-surface-2'
                  }`}
                >
                  <item.icon
                    className={`mr-3 h-5 w-5 ${
                      item.current ? 'text-primary' : 'text-text-secondary group-hover:text-primary'
                    }`}
                  />
                  {item.name}
                </a>
              ))}
            </nav>

            {/* User info and logout */}
            <div className="border-t border-border p-4">
              <div className="flex items-center space-x-3 mb-3">
                <div className="w-8 h-8 bg-surface-2 rounded-full flex items-center justify-center">
                  <span className="text-sm font-medium text-text-primary">
                    {admin.user?.username?.[0]?.toUpperCase() || 'A'}
                  </span>
                </div>
                <div className="flex-1 min-w-0">
                  <p className="text-sm font-medium text-text-primary truncate">
                    {admin.user?.username || 'Admin'}
                  </p>
                  <p className="text-xs text-text-secondary">
                    Administrator
                  </p>
                </div>
              </div>
              <button
                onClick={handleLogout}
                className="w-full flex items-center px-3 py-2 text-sm text-red-400 hover:bg-red-400/10 rounded-lg transition-colors"
              >
                <LogOut className="mr-3 h-4 w-4" />
                {t('admin.logout')}
              </button>
            </div>
          </div>
        </div>
      </div>

      {/* Mobile sidebar */}
      <AnimatePresence>
        {sidebarOpen && (
          <>
            <motion.div
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              exit={{ opacity: 0 }}
              className="fixed inset-0 bg-black bg-opacity-50 z-40 lg:hidden"
              onClick={() => setSidebarOpen(false)}
            />
            <motion.div
              initial={{ x: -280 }}
              animate={{ x: 0 }}
              exit={{ x: -280 }}
              className="fixed inset-y-0 left-0 z-50 w-70 lg:hidden"
            >
              <div className="admin-sidebar h-full">
                <div className="flex h-full flex-col">
                  <div className="flex h-20 items-center justify-between px-6 border-b border-border">
                    <div className="flex items-center space-x-3">
                      <div className="w-8 h-8 bg-gradient-to-br from-primary to-secondary rounded-full flex items-center justify-center">
                        <span className="text-sm font-bold text-background">V</span>
                      </div>
                      <div>
                        <h1 className="font-montserrat font-bold text-text-primary">
                          Vuala Admin
                        </h1>
                      </div>
                    </div>
                    <button
                      onClick={() => setSidebarOpen(false)}
                      className="p-2 hover:bg-surface-2 rounded-lg"
                    >
                      <X className="w-5 h-5 text-text-secondary" />
                    </button>
                  </div>

                  <nav className="flex-1 px-4 py-6 space-y-2 overflow-y-auto">
                    {navigationItems.map((item) => (
                      <a
                        key={item.name}
                        href={item.href}
                        className={`admin-nav-item group flex items-center px-3 py-3 text-sm font-medium rounded-lg transition-colors ${
                          item.current
                            ? 'bg-primary/10 text-primary'
                            : 'text-text-secondary hover:text-primary hover:bg-surface-2'
                        }`}
                        onClick={() => setSidebarOpen(false)}
                      >
                        <item.icon
                          className={`mr-3 h-5 w-5 ${
                            item.current ? 'text-primary' : 'text-text-secondary group-hover:text-primary'
                          }`}
                        />
                        {item.name}
                      </a>
                    ))}
                  </nav>

                  <div className="border-t border-border p-4">
                    <div className="flex items-center space-x-3 mb-3">
                      <div className="w-8 h-8 bg-surface-2 rounded-full flex items-center justify-center">
                        <span className="text-sm font-medium text-text-primary">
                          {admin.user?.username?.[0]?.toUpperCase() || 'A'}
                        </span>
                      </div>
                      <div className="flex-1 min-w-0">
                        <p className="text-sm font-medium text-text-primary truncate">
                          {admin.user?.username || 'Admin'}
                        </p>
                        <p className="text-xs text-text-secondary">
                          Administrator
                        </p>
                      </div>
                    </div>
                    <button
                      onClick={handleLogout}
                      className="w-full flex items-center px-3 py-2 text-sm text-red-400 hover:bg-red-400/10 rounded-lg transition-colors"
                    >
                      <LogOut className="mr-3 h-4 w-4" />
                      {t('admin.logout')}
                    </button>
                  </div>
                </div>
              </div>
            </motion.div>
          </>
        )}
      </AnimatePresence>

      {/* Main content */}
      <div className="admin-main">
        {/* Mobile header */}
        <div className="lg:hidden">
          <div className="flex h-16 items-center justify-between px-4 border-b border-border bg-surface-1">
            <button
              onClick={() => setSidebarOpen(true)}
              className="p-2 hover:bg-surface-2 rounded-lg"
            >
              <Menu className="w-5 h-5 text-text-secondary" />
            </button>
            <div className="flex items-center space-x-3">
              <div className="w-6 h-6 bg-gradient-to-br from-primary to-secondary rounded-full flex items-center justify-center">
                <span className="text-xs font-bold text-background">V</span>
              </div>
              <span className="font-montserrat font-bold text-text-primary">
                Vuala Admin
              </span>
            </div>
            <div className="w-9" /> {/* Spacer for centering */}
          </div>
        </div>

        {/* Routes */}
        <Routes>
          <Route path="/" element={<Navigate to="/admin/dashboard" replace />} />
          <Route path="/dashboard" element={<AdminDashboard />} />
          <Route path="/content" element={<ContentManagement />} />
          <Route path="/photos" element={<PhotoReview />} />
          <Route path="/reports" element={<ReportsPanel />} />
          <Route path="/users" element={<UserManagement />} />
        </Routes>
      </div>
    </div>
  )
}

export default AdminPanel