// Funcin para obtener todas las atracciones de un pas
export const getAttractionsByCountry = (countryCode) => {
  return WORLD_EMBLEMATIC_ATTRACTIONS[countryCode] || { regions: {} };
};

// Funcin para obtener atracciones por ciudad
export const getAttractionsByCity = (countryCode, regionName, cityName) => {
  const country = WORLD_EMBLEMATIC_ATTRACTIONS[countryCode];
  if (!country || !country.regions[regionName] || !country.regions[regionName].cities[cityName]) {
    return [];
  }
  return country.regions[regionName].cities[cityName].attractions;
};

// Funcin para obtener atraccin por ID
export const getAttractionById = (attractionId) => {
  for (const [countryCode, countryData] of Object.entries(WORLD_EMBLEMATIC_ATTRACTIONS)) {
    for (const [regionName, regionData] of Object.entries(countryData.regions)) {
      for (const [cityName, cityData] of Object.entries(regionData.cities)) {
        for (const attraction of cityData.attractions) {
          if (attraction.id === attractionId) {
            return attraction;
          }
        }
      }
    }
  }
  return null;
};

