import React, { useState, useEffect } from 'react'
import { useTranslation } from 'react-i18next'
import { motion, AnimatePresence } from 'framer-motion'
import { 
  Search, 
  MapPin, 
  Camera, 
  ChevronRight, 
  Clock,
  TrendingUp,
  X
} from 'lucide-react'
import { useAppStore } from '../store/appStore'

const SearchPanel = () => {
  const { t } = useTranslation()
  const {
    searchQuery,
    searchResults,
    searchSteps,
    selectedCountry,
    selectedRegion,
    selectedCity,
    selectedAttraction,
    searchHistory,
    countries,
    regions,
    cities,
    attractions,
    setSearchQuery,
    setSearchResults,
    addToSearchHistory,
    setSearchStep,
    resetSearch
  } = useAppStore()

  const [activeStep, setActiveStep] = useState('country')
  const [isExpanded, setIsExpanded] = useState(false)

  // Mock data - In production, these would come from APIs
  const mockCountries = [
    { id: 1, name: 'United States', code: 'US', flag: '' },
    { id: 2, name: 'France', code: 'FR', flag: '' },
    { id: 3, name: 'Japan', code: 'JP', flag: '' },
    { id: 4, name: 'Italy', code: 'IT', flag: '' },
    { id: 5, name: 'Spain', code: 'ES', flag: '' },
    { id: 6, name: 'Thailand', code: 'TH', flag: '' },
    { id: 7, name: 'Australia', code: 'AU', flag: '' },
    { id: 8, name: 'Brazil', code: 'BR', flag: '' }
  ]

  const mockRegions = {
    1: [ // US
      { id: 11, name: 'California', countryId: 1 },
      { id: 12, name: 'New York', countryId: 1 },
      { id: 13, name: 'Florida', countryId: 1 }
    ],
    2: [ // France
      { id: 21, name: 'le-de-France', countryId: 2 },
      { id: 22, name: 'Provence-Alpes-Cte d\'Azur', countryId: 2 },
      { id: 23, name: 'Normandy', countryId: 2 }
    ]
  }

  const mockCities = {
    11: [ // California
      { id: 111, name: 'Los Angeles', regionId: 11 },
      { id: 112, name: 'San Francisco', regionId: 11 },
      { id: 113, name: 'San Diego', regionId: 11 }
    ],
    12: [ // New York
      { id: 121, name: 'New York City', regionId: 12 },
      { id: 122, name: 'Buffalo', regionId: 12 },
      { id: 123, name: 'Albany', regionId: 12 }
    ]
  }

  const mockAttractions = {
    111: [ // Los Angeles
      { id: 1111, name: 'Hollywood Sign', type: 'landmark', lat: 34.1341, lng: -118.3215 },
      { id: 1112, name: 'Griffith Observatory', type: 'attraction', lat: 34.1184, lng: -118.3004 },
      { id: 1113, name: 'Santa Monica Pier', type: 'attraction', lat: 34.0087, lng: -118.4973 }
    ],
    112: [ // San Francisco
      { id: 1121, name: 'Golden Gate Bridge', type: 'landmark', lat: 37.8199, lng: -122.4783 },
      { id: 1122, name: 'Alcatraz Island', type: 'attraction', lat: 37.8060, lng: -122.4167 },
      { id: 1123, name: 'Lombard Street', type: 'landmark', lat: 37.8021, lng: -122.4187 }
    ]
  }

  const popularSearches = [
    'Eiffel Tower', 'Tokyo', 'Machu Picchu', 'Santorini', 'Bali', 'New York'
  ]

  // Filter data based on search query
  const getFilteredData = (step, query = '') => {
    const data = getStepData(step)
    if (!query) return data.slice(0, 8) // Show first 8 items when no query
    
    return data.filter(item => 
      item.name.toLowerCase().includes(query.toLowerCase())
    ).slice(0, 10)
  }

  const getStepData = (step) => {
    switch (step) {
      case 'country':
        return mockCountries
      case 'region':
        return selectedCountry ? mockRegions[selectedCountry.id] || [] : []
      case 'city':
        return selectedRegion ? mockCities[selectedRegion.id] || [] : []
      case 'attraction':
        return selectedCity ? mockAttractions[selectedCity.id] || [] : []
      default:
        return []
    }
  }

  const handleStepChange = (step) => {
    setActiveStep(step)
    setSearchStep(step, null)
    
    // Reset subsequent steps
    if (step === 'country') {
      setSearchStep('region', null)
      setSearchStep('city', null)
      setSearchStep('attraction', null)
    } else if (step === 'region') {
      setSearchStep('city', null)
      setSearchStep('attraction', null)
    } else if (step === 'city') {
      setSearchStep('attraction', null)
    }
  }

  const handleItemSelect = (item, step) => {
    setSearchStep(step, item)
    addToSearchHistory(item.name)
    
    // Move to next step
    const nextStep = getNextStep(step)
    if (nextStep) {
      setActiveStep(nextStep)
    }
  }

  const getNextStep = (currentStep) => {
    const steps = ['country', 'region', 'city', 'attraction']
    const currentIndex = steps.indexOf(currentStep)
    return currentIndex < steps.length - 1 ? steps[currentIndex + 1] : null
  }

  const getStepProgress = () => {
    const steps = ['country', 'region', 'city', 'attraction']
    const completedSteps = steps.filter(step => searchSteps[step])
    return (completedSteps.length / steps.length) * 100
  }

  return (
    <motion.div
      initial={{ x: -100, opacity: 0 }}
      animate={{ x: 0, opacity: 1 }}
      className="h-full glass border-r border-border flex flex-col"
    >
      {/* Header */}
      <div className="p-6 border-b border-border">
        <div className="flex items-center justify-between mb-4">
          <h2 className="text-xl font-montserrat font-semibold text-text-primary">
            {t('common.search')}
          </h2>
          {isExpanded && (
            <button
              onClick={() => setIsExpanded(false)}
              className="p-2 hover:bg-surface-2 rounded-lg transition-colors"
            >
              <X className="w-5 h-5 text-text-secondary" />
            </button>
          )}
        </div>
        
        {/* Search Progress */}
        <div className="mb-4">
          <div className="flex items-center justify-between text-sm text-text-secondary mb-2">
            <span>Progress</span>
            <span>{Math.round(getStepProgress())}%</span>
          </div>
          <div className="w-full bg-surface-2 rounded-full h-2">
            <motion.div
              className="h-2 bg-gradient-to-r from-primary to-secondary rounded-full"
              initial={{ width: 0 }}
              animate={{ width: `${getStepProgress()}%` }}
              transition={{ duration: 0.5 }}
            />
          </div>
        </div>

        {/* Step Indicators */}
        <div className="flex items-center space-x-2 text-sm">
          {['country', 'region', 'city', 'attraction'].map((step, index) => (
            <React.Fragment key={step}>
              <div className={`flex items-center space-x-1 ${
                searchSteps[step] ? 'text-primary' : 'text-text-secondary'
              }`}>
                <div className={`w-2 h-2 rounded-full ${
                  searchSteps[step] ? 'bg-primary' : 'bg-surface-2'
                }`} />
                <span className="capitalize">{t(`search.${step}`)}</span>
              </div>
              {index < 3 && (
                <ChevronRight className="w-4 h-4 text-text-secondary" />
              )}
            </React.Fragment>
          ))}
        </div>
      </div>

      {/* Search Steps */}
      <div className="flex-1 overflow-y-auto custom-scrollbar">
        {/* Step 1: Country */}
        <motion.div
          className={`p-6 border-b border-border transition-all duration-300 ${
            activeStep === 'country' ? 'bg-surface-1/50' : ''
          }`}
        >
          <h3 className="font-montserrat font-medium text-text-primary mb-4 flex items-center">
            <MapPin className="w-5 h-5 mr-2 text-primary" />
            {t('search.country')}
          </h3>
          
          <div className="space-y-3">
            {getFilteredData('country', searchSteps.country?.name || '').map((country) => (
              <motion.button
                key={country.id}
                onClick={() => handleItemSelect(country, 'country')}
                className={`w-full flex items-center space-x-3 p-3 rounded-lg border transition-all ${
                  selectedCountry?.id === country.id
                    ? 'border-primary bg-primary/10 text-primary'
                    : 'border-border hover:border-primary/50 hover:bg-surface-2'
                }`}
                whileHover={{ scale: 1.02 }}
                whileTap={{ scale: 0.98 }}
              >
                <span className="text-2xl">{country.flag}</span>
                <div className="flex-1 text-left">
                  <div className="font-medium text-text-primary">{country.name}</div>
                  <div className="text-sm text-text-secondary">{country.code}</div>
                </div>
                {selectedCountry?.id === country.id && (
                  <div className="w-2 h-2 bg-primary rounded-full" />
                )}
              </motion.button>
            ))}
          </div>
        </motion.div>

        {/* Step 2: Region (only show if country is selected) */}
        {selectedCountry && (
          <motion.div
            initial={{ opacity: 0, height: 0 }}
            animate={{ opacity: 1, height: 'auto' }}
            className={`p-6 border-b border-border transition-all duration-300 ${
              activeStep === 'region' ? 'bg-surface-1/50' : ''
            }`}
          >
            <h3 className="font-montserrat font-medium text-text-primary mb-4">
              {t('search.region')}
            </h3>
            
            <div className="space-y-3">
              {getFilteredData('region', searchSteps.region?.name || '').map((region) => (
                <motion.button
                  key={region.id}
                  onClick={() => handleItemSelect(region, 'region')}
                  className={`w-full flex items-center space-x-3 p-3 rounded-lg border transition-all ${
                    selectedRegion?.id === region.id
                      ? 'border-primary bg-primary/10 text-primary'
                      : 'border-border hover:border-primary/50 hover:bg-surface-2'
                  }`}
                  whileHover={{ scale: 1.02 }}
                  whileTap={{ scale: 0.98 }}
                >
                  <div className="flex-1 text-left">
                    <div className="font-medium text-text-primary">{region.name}</div>
                  </div>
                  {selectedRegion?.id === region.id && (
                    <div className="w-2 h-2 bg-primary rounded-full" />
                  )}
                </motion.button>
              ))}
            </div>
          </motion.div>
        )}

        {/* Step 3: City (only show if region is selected) */}
        {selectedRegion && (
          <motion.div
            initial={{ opacity: 0, height: 0 }}
            animate={{ opacity: 1, height: 'auto' }}
            className={`p-6 border-b border-border transition-all duration-300 ${
              activeStep === 'city' ? 'bg-surface-1/50' : ''
            }`}
          >
            <h3 className="font-montserrat font-medium text-text-primary mb-4">
              {t('search.city')}
            </h3>
            
            <div className="space-y-3">
              {getFilteredData('city', searchSteps.city?.name || '').map((city) => (
                <motion.button
                  key={city.id}
                  onClick={() => handleItemSelect(city, 'city')}
                  className={`w-full flex items-center space-x-3 p-3 rounded-lg border transition-all ${
                    selectedCity?.id === city.id
                      ? 'border-primary bg-primary/10 text-primary'
                      : 'border-border hover:border-primary/50 hover:bg-surface-2'
                  }`}
                  whileHover={{ scale: 1.02 }}
                  whileTap={{ scale: 0.98 }}
                >
                  <div className="flex-1 text-left">
                    <div className="font-medium text-text-primary">{city.name}</div>
                  </div>
                  {selectedCity?.id === city.id && (
                    <div className="w-2 h-2 bg-primary rounded-full" />
                  )}
                </motion.button>
              ))}
            </div>
          </motion.div>
        )}

        {/* Step 4: Attractions (only show if city is selected) */}
        {selectedCity && (
          <motion.div
            initial={{ opacity: 0, height: 0 }}
            animate={{ opacity: 1, height: 'auto' }}
            className={`p-6 transition-all duration-300 ${
              activeStep === 'attraction' ? 'bg-surface-1/50' : ''
            }`}
          >
            <h3 className="font-montserrat font-medium text-text-primary mb-4 flex items-center">
              <Camera className="w-5 h-5 mr-2 text-secondary" />
              {t('search.attraction')}
            </h3>
            
            <div className="space-y-3">
              {getFilteredData('attraction', searchSteps.attraction?.name || '').map((attraction) => (
                <motion.button
                  key={attraction.id}
                  onClick={() => handleItemSelect(attraction, 'attraction')}
                  className={`w-full flex items-center space-x-3 p-3 rounded-lg border transition-all ${
                    selectedAttraction?.id === attraction.id
                      ? 'border-secondary bg-secondary/10 text-secondary'
                      : 'border-border hover:border-secondary/50 hover:bg-surface-2'
                  }`}
                  whileHover={{ scale: 1.02 }}
                  whileTap={{ scale: 0.98 }}
                >
                  <div className="flex-1 text-left">
                    <div className="font-medium text-text-primary">{attraction.name}</div>
                    <div className="text-sm text-text-secondary capitalize">{attraction.type}</div>
                  </div>
                  {selectedAttraction?.id === attraction.id && (
                    <div className="w-2 h-2 bg-secondary rounded-full" />
                  )}
                </motion.button>
              ))}
            </div>
          </motion.div>
        )}

        {/* Search History */}
        {searchHistory.length > 0 && (
          <div className="p-6 border-t border-border">
            <h4 className="font-montserrat font-medium text-text-primary mb-3 flex items-center">
              <Clock className="w-4 h-4 mr-2" />
              Recent Searches
            </h4>
            <div className="space-y-2">
              {searchHistory.slice(0, 5).map((item, index) => (
                <button
                  key={index}
                  onClick={() => setSearchQuery(item)}
                  className="w-full text-left text-sm text-text-secondary hover:text-primary transition-colors"
                >
                  {item}
                </button>
              ))}
            </div>
          </div>
        )}

        {/* Popular Searches */}
        <div className="p-6 border-t border-border">
          <h4 className="font-montserrat font-medium text-text-primary mb-3 flex items-center">
            <TrendingUp className="w-4 h-4 mr-2" />
            Popular
          </h4>
          <div className="flex flex-wrap gap-2">
            {popularSearches.map((item, index) => (
              <button
                key={index}
                onClick={() => setSearchQuery(item)}
                className="px-3 py-1 text-sm bg-surface-2 hover:bg-primary hover:text-background text-text-secondary rounded-full transition-all"
              >
                {item}
              </button>
            ))}
          </div>
        </div>
      </div>
    </motion.div>
  )
}

export default SearchPanel