import React, { useState } from 'react'
import { motion } from 'framer-motion'
import { MapPin, Plus, Edit, Trash2, Search } from 'lucide-react'
import { useTranslation } from 'react-i18next'

const ContentManagement = () => {
  const { t } = useTranslation()
  const [activeTab, setActiveTab] = useState('countries')
  const [searchQuery, setSearchQuery] = useState('')

  const tabs = [
    { id: 'countries', name: 'Countries', count: 195 },
    { id: 'regions', name: 'Regions', count: 1247 },
    { id: 'cities', name: 'Cities', count: 4521 },
    { id: 'attractions', name: 'Attractions', count: 12847 }
  ]

  // Mock data
  const mockData = {
    countries: [
      { id: 1, name: 'United States', code: 'US', flag: '', status: 'active', destinations: 1234 },
      { id: 2, name: 'France', code: 'FR', flag: '', status: 'active', destinations: 987 },
      { id: 3, name: 'Japan', code: 'JP', flag: '', status: 'active', destinations: 856 },
      { id: 4, name: 'Italy', code: 'IT', flag: '', status: 'active', destinations: 743 },
      { id: 5, name: 'Spain', code: 'ES', flag: '', status: 'active', destinations: 654 }
    ],
    regions: [
      { id: 1, name: 'California', country: 'United States', status: 'active', cities: 58 },
      { id: 2, name: 'le-de-France', country: 'France', status: 'active', cities: 8 },
      { id: 3, name: 'Kanto', country: 'Japan', status: 'active', cities: 14 }
    ],
    cities: [
      { id: 1, name: 'Los Angeles', region: 'California', country: 'United States', status: 'active', attractions: 234 },
      { id: 2, name: 'Paris', region: 'le-de-France', country: 'France', status: 'active', attractions: 456 },
      { id: 3, name: 'Tokyo', region: 'Kanto', country: 'Japan', status: 'active', attractions: 321 }
    ],
    attractions: [
      { id: 1, name: 'Eiffel Tower', city: 'Paris', country: 'France', type: 'landmark', status: 'active', photos: 1234 },
      { id: 2, name: 'Statue of Liberty', city: 'New York', country: 'United States', type: 'landmark', status: 'active', photos: 987 },
      { id: 3, name: 'Tokyo Tower', city: 'Tokyo', country: 'Japan', type: 'landmark', status: 'active', photos: 756 }
    ]
  }

  const getCurrentData = () => {
    return mockData[activeTab] || []
  }

  const getFilteredData = () => {
    const data = getCurrentData()
    if (!searchQuery) return data
    
    return data.filter(item =>
      item.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
      (item.country && item.country.toLowerCase().includes(searchQuery.toLowerCase())) ||
      (item.region && item.region.toLowerCase().includes(searchQuery.toLowerCase()))
    )
  }

  const renderTable = () => {
    const data = getFilteredData()
    
    const getTableHeaders = () => {
      switch (activeTab) {
        case 'countries':
          return ['Country', 'Code', 'Status', 'Destinations', 'Actions']
        case 'regions':
          return ['Region', 'Country', 'Status', 'Cities', 'Actions']
        case 'cities':
          return ['City', 'Region', 'Country', 'Status', 'Attractions', 'Actions']
        case 'attractions':
          return ['Attraction', 'City', 'Country', 'Type', 'Status', 'Photos', 'Actions']
        default:
          return []
      }
    }

    const getTableRows = () => {
      return data.map((item) => (
        <tr key={item.id} className="hover:bg-surface-2/50">
          <td className="px-6 py-4">
            <div className="flex items-center space-x-3">
              {item.flag && <span className="text-2xl">{item.flag}</span>}
              <span className="font-medium text-text-primary">{item.name}</span>
            </div>
          </td>
          {activeTab !== 'countries' && activeTab !== 'attractions' && (
            <td className="px-6 py-4 text-text-secondary">
              {item.country || item.region}
            </td>
          )}
          {activeTab === 'cities' && (
            <td className="px-6 py-4 text-text-secondary">{item.region}</td>
          )}
          {activeTab === 'attractions' && (
            <>
              <td className="px-6 py-4 text-text-secondary">{item.city}</td>
              <td className="px-6 py-4">
                <span className="inline-flex items-center px-2 py-1 rounded-full text-xs font-medium bg-primary/20 text-primary">
                  {item.type}
                </span>
              </td>
            </>
          )}
          <td className="px-6 py-4">
            <span className={`inline-flex items-center px-2 py-1 rounded-full text-xs font-medium ${
              item.status === 'active' 
                ? 'bg-green-500/20 text-green-400' 
                : 'bg-red-500/20 text-red-400'
            }`}>
              {item.status}
            </span>
          </td>
          <td className="px-6 py-4 text-text-secondary">
            {item.destinations || item.cities || item.attractions || item.photos}
          </td>
          <td className="px-6 py-4">
            <div className="flex items-center space-x-2">
              <button className="p-1 text-text-secondary hover:text-primary transition-colors">
                <Edit className="w-4 h-4" />
              </button>
              <button className="p-1 text-text-secondary hover:text-red-400 transition-colors">
                <Trash2 className="w-4 h-4" />
              </button>
            </div>
          </td>
        </tr>
      ))
    }

    return (
      <div className="overflow-x-auto">
        <table className="min-w-full divide-y divide-border">
          <thead className="bg-surface-2/50">
            <tr>
              {getTableHeaders().map((header) => (
                <th
                  key={header}
                  className="px-6 py-3 text-left text-xs font-medium text-text-secondary uppercase tracking-wider"
                >
                  {header}
                </th>
              ))}
            </tr>
          </thead>
          <tbody className="divide-y divide-border">
            {getTableRows()}
          </tbody>
        </table>
      </div>
    )
  }

  return (
    <div className="p-6 space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-montserrat font-bold text-text-primary">
            {t('admin.content')}
          </h1>
          <p className="text-text-secondary mt-1">
            Manage destinations, cities, and attractions
          </p>
        </div>
        
        <button className="flex items-center space-x-2 bg-primary text-background px-4 py-2 rounded-lg hover:bg-primary/90 transition-colors">
          <Plus className="w-4 h-4" />
          <span>Add New</span>
        </button>
      </div>

      {/* Tabs */}
      <div className="border-b border-border">
        <nav className="flex space-x-8">
          {tabs.map((tab) => (
            <button
              key={tab.id}
              onClick={() => setActiveTab(tab.id)}
              className={`py-2 px-1 border-b-2 font-medium text-sm transition-colors ${
                activeTab === tab.id
                  ? 'border-primary text-primary'
                  : 'border-transparent text-text-secondary hover:text-text-primary hover:border-text-secondary'
              }`}
            >
              {tab.name}
              <span className="ml-2 bg-surface-2 text-text-secondary py-0.5 px-2 rounded-full text-xs">
                {tab.count}
              </span>
            </button>
          ))}
        </nav>
      </div>

      {/* Search and Filters */}
      <div className="flex items-center space-x-4">
        <div className="relative flex-1 max-w-md">
          <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 w-4 h-4 text-text-secondary" />
          <input
            type="text"
            placeholder={`Search ${activeTab}...`}
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            className="w-full pl-10 pr-4 py-2 bg-surface-2 border border-border text-text-primary rounded-lg focus:outline-none focus:ring-2 focus:ring-primary focus:border-transparent"
          />
        </div>
        
        <select className="px-3 py-2 bg-surface-2 border border-border text-text-primary rounded-lg focus:outline-none focus:ring-2 focus:ring-primary">
          <option>All Status</option>
          <option>Active</option>
          <option>Inactive</option>
        </select>
      </div>

      {/* Content Table */}
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        className="glass rounded-xl border border-border overflow-hidden"
      >
        {renderTable()}
      </motion.div>

      {/* Pagination */}
      <div className="flex items-center justify-between">
        <div className="text-sm text-text-secondary">
          Showing 1 to {getCurrentData().length} of {getCurrentData().length} entries
        </div>
        <div className="flex items-center space-x-2">
          <button className="px-3 py-1 text-sm bg-surface-2 text-text-secondary rounded hover:bg-surface-1 transition-colors">
            Previous
          </button>
          <button className="px-3 py-1 text-sm bg-primary text-background rounded">
            1
          </button>
          <button className="px-3 py-1 text-sm bg-surface-2 text-text-secondary rounded hover:bg-surface-1 transition-colors">
            2
          </button>
          <button className="px-3 py-1 text-sm bg-surface-2 text-text-secondary rounded hover:bg-surface-1 transition-colors">
            Next
          </button>
        </div>
      </div>
    </div>
  )
}

export default ContentManagement